<template>
    <wn-dialog title="title" :visible="true" :modal="false" :modal-append-to-body="false" style="width:100%;position:inherit;" size="full">
        <div class="container-layout-zone">
            <slot>
                <div class="slot-tip tip" style="height:100px">body</div>
            </slot>
        </div>
        <div slot="footer" class="dialog-footer">
            <div class="container-layout-zone" slot-name="footer">
                <slot name="footer">
                    <div class="slot-tip tip">footer</div>
                </slot>
            </div>
        </div>
    </wn-dialog>
</template>
<script>
export default {
    name: 'dialogContainer'

}

</script>
<style scoped>

</style>



