/* eslint-disable no-new-func */
import * as utils from './utils'
import * as cst from './const'

const QUOTE = '%%'
let format = utils.format

function wrapQuote(str) {
  return QUOTE + str + QUOTE
}
const valueTpl = '_vm.{0}'

const inputTpl = 'function (value) {_vm.{0}=value;_vm.$emit(\'input\', value)}'

const updateTpl = 'function (value) {_vm.{0}=value}'

const eventTpl = 'function ($event) {_vm.{0}}'

const vForTpl = 'e._l(_vm.{0},function(item){return {1}})'

const vBindTpl = 'e._b({0},_vm.{1})'

// global compile object
export const env = {
  // v-for method
  _l: function (data, f) {
    if (data && data.length) {
      return data.map(item => {
        return f(item)
      })
    }
    return []
  },
  // bind object
  _b: function (data, value) {
    if (!value) return data
    let hash
    for (const key in value) {
      hash = data.props || (data.props = {})
      if (!(key in hash)) hash[key] = value[key]
    }
    return data
  }
}

export function compileDataBind(vNode, path, args) {
  let newObj = {}
  let bindProp = args[0]
  newObj[bindProp] = wrapQuote(format(valueTpl, path))
  vNode.setAttr('props', newObj)
  // if bindProp is 'value' ,considering two way bind
  if (bindProp === 'value') {
    vNode.setAttr('on', {
      'input': wrapQuote(format(inputTpl, path))
    })
  }
  if (args[1] === 'sync') {
    let updateEvent = {}
    updateEvent['update:' + bindProp] = wrapQuote(format(updateTpl, path))
    vNode.setAttr('on', updateEvent)
  }
}

function vNodeRender(tag, attr, children) {
  return 'h(\'' + tag + '\',' + attr + ',[' + children + '])'
}

let replaceExp = /"?%%"?/g

function renderAttr(vNode) {
  return JSON.stringify(vNode.attr.native()).replace(replaceExp, '')
}

function hasArgs(method) {
  return method.match(cst.METHOD_ARGS_REG) != null
}

/**
 * handle event method
 * e.g test(form) ->test(_vm.form)
 * @param {*} method
 */
function compileArgs(method) {
  let argsExp = method.match(cst.METHOD_ARGS_REG)
  argsExp = argsExp[0]
  argsExp = argsExp.substr(1, argsExp.length - 2)
  let args = argsExp.split(',')
  args = args.map(arg => {
    return handleArgument(arg)
  })
  let argStr = '(' + args + ')'
  return method.replace(cst.METHOD_ARGS_REG, argStr)
}

let varChkExp = /^[A-Za-z]*$/g

function handleArgument(arg) {
  arg = arg.trim()
  if (varChkExp.test(arg)) return '_vm.' + arg
  return arg
}

/**
 * create event listener on vnode
 * @param {*} vNode
 * @param {*} event e.g. click
 * @param {*} method  method(arg1,arg2...)
 */
export function compileEventListener(vNode, event, methodStr) {
  let eventType = utils.eventType(event)
  event = utils.getProps(event)
  if (!methodStr) return
  let def = {}
  if (!hasArgs(methodStr)) {
    def[event] = wrapQuote(format(valueTpl, methodStr))
  } else {
    let compiledMethod = compileArgs(methodStr)
    def[event] = wrapQuote(format(eventTpl, compiledMethod))
  }
  vNode.setAttr(eventType, def)
}

/**
 * handle v-for
 * @param {*} vNode
 * @param {*} prop  loop props setting
 */
export function compileVforItem(vNode, prop) {
  let obj = {}
  obj[prop] = wrapQuote('item.' + prop)
  vNode.setAttr('props', obj)
}

function createRenderFunction(vNode) {
  if (!vNode) throw new Error('vNode can\'t be null')
  if ((typeof vNode) === 'string') return '\'' + vNode + '\''
  let children = []
  vNode.children.forEach(function (child) {
    let render = createRenderFunction(child)
    if (render) return children.push(render)
  })
  let renderStr
  let directives = vNode.getDirectives()
  if (directives) {
    renderStr = handleDirectives(directives, vNode, children)
  } else {
    renderStr = vNodeRender(vNode.tag, renderAttr(vNode), children)
  }
  return renderStr
}

function handleDirectives(directives, vNode, children) {
  if (!directives.value) return
  if (cst.V_FOR === directives.key) return handleVfor(directives.value, vNode, children)
  if (cst.V_BIND === directives.key) return handleVBind(directives.value, vNode, children)
}

function handleVfor(value, vNode, children) {
  return format(vForTpl, value, vNodeRender(vNode.tag, renderAttr(vNode), children))
}

function handleVBind(value, vNode, children) {
  let attr = renderAttr(vNode)
  attr = format(vBindTpl, attr, value)
  return vNodeRender(vNode.tag, attr, children)
}

function cpRenderFunction(vNode) {
  return new Function('h', 'e', this.cpRenderCode(vNode))
}

function cpRenderCode(vNode) {
  return 'var _vm=this;return ' + createRenderFunction(vNode)
}

function cpRenderFunstr(vNode) {
  return 'function(h,e){' + cpRenderCode(vNode) + '}'
}

export default {
  cpRenderFunction,
  cpRenderCode,
  cpRenderFunstr
}



