import compAttrs from '../json/baseCompAttrs.json'
import components from '../json/components.json'
import specialComps from '../json/specialComps.json'
import events from '../json/event.json'
import select from '../json/select.json'
import * as cst from '@/assets/js/const'
import pagePool from '@/assets/js/pagePool'

var stardComp = {}
var formComp = []
Object.keys(components).forEach(key => {
  Object.assign(stardComp, components[key])
  if (key === cst.FORM_TYPE) formComp = formComp.concat(Object.keys(components[key]))
})
Object.assign(stardComp, specialComps)

for (let key in compAttrs) {
  if (stardComp[key] && stardComp[key].event) compAttrs[key].push(new cst.EVENTS_OPTION(key))
}

export default {
  getComp: function () {
    return components
  },
  getStardComp: function () {
    return stardComp
  },
  getFormComp: function () {
    return formComp
  },
  getCompAttr: function () {
    return compAttrs
  },

  getEventList: function (type) {
    type = type || 'common'
    let res = events[type] || []
    return res
  },
  getSelect: function (type, pageId) {
    if (type === 'dyn-comp') {
      select[type] = []
      pagePool.getList().forEach(pageModule => {
        pageModule.pages.forEach(page => {
          if (page.name !== pageId) select[type].push(page.name)
        })
      })
    }
    let res = select[type] || []
    return res
  }
}



