import Vue from 'vue'
import {
  stardComp,
  layoutComp,
  toEditVnode
} from './component'
import * as utils from './utils'
import pagePool from './pagePool'
import * as cst from '@/assets/js/const'
import * as wd from '@/assets/js/widget'
import cfg from './config'
import pageCompile from '@/assets/js/pageCompile'

const eventBus = new Vue()
const dragStartEvent = 'drag-start'
const dragEndEvent = 'drag-end'
const editorItemClickEvent = 'item-click'
const optionsUpdateEvent = 'item-option-update'
const editorItemDeleteEvent = 'item-delete'
const layoutChangeEvent = 'layout-change'

const EditorUtils = {

  onDragStart: function (callback) {
    eventBus.$on(dragStartEvent, callback)
  },
  onDragEnd: function (callback) {
    eventBus.$on(dragEndEvent, callback)
  },
  emitItemClick: function (vNode) {
    eventBus.$emit(editorItemClickEvent, vNode)
  },
  onItemClick: function (callback) {
    eventBus.$on(editorItemClickEvent, callback)
  },
  onOptionsUpdate: function (callback) {
    eventBus.$on(optionsUpdateEvent, callback)
  },
  emitOptionsUpdate: function (options) {
    eventBus.$emit(optionsUpdateEvent, options)
  },
  emitItemDelete: function (nodeKey, compKey) {
    eventBus.$emit(editorItemDeleteEvent, nodeKey, compKey)
  },
  onItemDelete: function (callback) {
    eventBus.$on(editorItemDeleteEvent, callback)
  },
  emitLayoutChange: function (action) {
    eventBus.$emit(layoutChangeEvent, action)
  },
  onLayoutChange: function (callback) {
    eventBus.$on(layoutChangeEvent, callback)
  },
  clearEventBus: function () {
    eventBus.$off()
  }
}

/**
 * Core object
 * @param {*} data
 * if data is string,return html element by data
 * if data is empty, return div
 * if data is object ,try to build vnode tree
 */
function Editor(data) {
  this.seq = -1
  this.genKey = this.genKey.bind(this)
  if (typeof data === 'string' || data == null) {
    this.root = stardComp.create(data, this.genKey)
  } else if (data instanceof Object) {
    this.root = toEditVnode(data, this.genKey)
  }
  this.root.genKey = this.genKey
}

Editor.prototype.genKey = function () {
  return this.seq++
}

Editor.prototype.getData = function () {
  return this.root
}
/**
 * Find vNode by key
 */
Editor.prototype.findByKey = function (key, vNode) {
  if (key == null) return undefined
  key = Number(key)
  if (!vNode) vNode = this.root
  if (vNode.key === key) return vNode
  let child
  let target
  for (let i in vNode.children) {
    child = vNode.children[i]
    target = this.findByKey(key, child)
    if (target) return target
  }

  return undefined
}
/**
Update component attributes
 */
Editor.prototype.updateVnodeOpts = function (options) {
  if (!options || options.length === 0) return
  var vNode = this.findByKey(options.root)
  if (!vNode) return
  this.updateOpt(vNode, options)
}

Editor.prototype.updateOpt = function (vNode, options) {
  const nodeKey = vNode.key
  const opt = options[nodeKey]
  if (opt) vNode.setOptions(opt)
  vNode.children.forEach(child => {
    this.updateOpt(child, options)
  })
}
/**
 * Generate widgets with value for component attributes by key
 */
Editor.prototype.getWidgetByKey = function (nodeKey) {
  let widgets = []
  let vNode = this.findByKey(nodeKey)
  if (!vNode) return widgets
  return this.getWidgetByNode(vNode)
}

Editor.prototype.getWidgetByNode = function (vNode) {
  let widgets = []
  let compAttrs = cfg.getCompAttr()
  let widget = compAttrs[vNode.type]
  if (!widget) {
    let plugin = pagePool.getPlugin(vNode.type)
    widget = plugin && plugin.options
  }
  if (Array.isArray(widget)) {
    widget.forEach(item => {
      const opt = wd.getOptByProp(item.prop, vNode.options)
      const order = compAttrs['widget-order'][item.widget] || 1
      widgets.push({
        key: vNode.key,
        widget: item,
        opt: opt,
        order: order
      })
    })
  }
  // if has limit, not drill down to child widgets
  if (compAttrs['widget-limit'][vNode.type]) return widgets
  vNode.children.forEach(child => {
    let childWidget = this.getWidgetByNode(child)
    widgets = widgets.concat(childWidget)
  })
  // order widgets
  widgets.sort((a, b) => {
    return a.order - b.order
  })
  return widgets
}

/**
 * add layout node
 * if have layoutKey,need to calculate new layout position
 */
Editor.prototype.addLayout = function (type, layoutKey, action, slotName) {
  if (layoutKey != null) {
    let existNode = this.findByKey(layoutKey)
    var parent = existNode.parent
    var index = parent.children.indexOf(existNode)
    if (action === 'down') index++
  } else {
    parent = this.root
  }
  let layout = layoutComp.create(type, this.genKey)
  if (slotName != null) layout.setAttr('slot', slotName)
  if (type === 'form-layout') {
    let defaultLayout = []
    defaultLayout.push(utils.createDefaultRow())
    layout.options.push({
      prop: cst.LAYOUT_PROPS,
      value: defaultLayout
    })
  }
  parent.addChild(layout, index)
}
/**
 * add component item
 * editor item is always used for wrapp component item
 * use layout type to check whether component item need to be wrapped
 * e.g form-layout need to wrapped by form-item
 */
Editor.prototype.add = function (itemType, itemKey, layoutType) {
  let zoneItemNode = this.findByKey(itemKey)
  if (!zoneItemNode) throw new Error('zone item not found,key:' + itemKey)
  // if drag item to grid layout directly, then create layout new row
  if (layoutType === 'new-row') {
    let layoutNode = zoneItemNode
    layoutType = layoutNode.type
    zoneItemNode = layoutComp.updateLayout(layoutNode, this.genKey, cst.COPY_LAYOUT_LAST_ROW)
  }

  const item = layoutComp.createItem(layoutType, itemType, this.genKey)
  zoneItemNode.addChild(item)
  zoneItemNode.changed = true
}

/**
 * edit layout
 * editInfo-layout key, row number,row data
 */
Editor.prototype.editLayout = function (editInfo) {
  let layoutNode = this.findByKey(editInfo.layoutKey)
  layoutComp.updateLayout(layoutNode, this.genKey, editInfo)
}

/**
 * move item to other item container
 * moveKey is moved item container key
 * targetKey is target item container key
 */
Editor.prototype.move = function (layoutTo, layoutFrom, movedKey, targetKey) {
  if (movedKey === targetKey) return
  if (layoutTo !== layoutFrom) {
    utils.message('warning', cst.MOVE_DIFF_LAYOUT_WARN)
    return
  }
  let movedNode = this.findByKey(movedKey)
  let targetNode = this.findByKey(targetKey)
  let movedChild = movedNode.children.length > 0 ? movedNode.children[0] : null
  let targetChild = targetNode.children.length > 0 ? targetNode.children[0] : null
  targetNode.children = []
  movedNode.children = []
  targetNode.changed = true
  movedNode.changed = true
  if (movedChild) targetNode.addChild(movedChild)
  if (targetChild) movedNode.addChild(targetChild)
}

Editor.prototype.deleteNodeByKey = function (nodeKey) {
  var vNode = this.findByKey(nodeKey)
  if (vNode == null) {
    return
  }
  const parent = vNode.parent
  const index = parent.children.indexOf(vNode)
  vNode.parent.children.splice(index, 1)
  vNode.parent.changed = true
}

var instance = new Valkyrja()
function main() {
  return instance
}

function Valkyrja() {
  return this
}

Valkyrja.prototype.init = function (conf) {
  if (!conf||!conf.el|!conf.id || !conf.name) {
    console.error('el、id、name cannot be null')
  }
  this.id = conf.id
  this.name = conf.name
  this.saveListener = conf.saveListener
  if (this.init_after) {
    this.init_after(conf)
  }
}

Valkyrja.prototype.save = function (moduleId, pageInfo, callback) {
  if (this.saveListener) this.saveListener(pageInfo)
}

/**
 * return vue class by page id,it is used for production(class will be cached)
 */
Valkyrja.prototype.addApi = function (moduleName, target) {
  if (!moduleName || moduleName.length === 0 || !target) {
    console.warn('Failed to register:name[' + moduleName + '],' + target)
    return
  }
  pagePool.register(moduleName, target)
}

/**
 * register biz plugins for editor
 */
Valkyrja.prototype.addComp = function (name, target) {
  if (!name || name.length === 0 || !target) {
    console.warn('Failed to add component :name[' + name + '],' + target)
    return
  }
  Vue.component(name, target)
  let label
  if (target.plugin && target.plugin.label) {
    label = target.plugin.label
  } else {
    label = name
  }
  let pluginInfo = {
    label: label,
    tag: name
  }
  let comp = cfg.getComp()
  comp['plugin'][name] = pluginInfo
  stardComp[name] = pluginInfo
  let plugin = target.plugin || {
    options: []
  }
  if (!plugin.options) plugin.options = []
  plugin.options = plugin.options.concat(cst.PLUGIN_DEFAULT_OPTION)
  pagePool.putPlugin(name, target.plugin)
}

Valkyrja.prototype.getPages = function (moduleId, callback) {
  pagePool.getModulePage(moduleId, callback)
}

Valkyrja.prototype.createForm = function (conf) {
  let PageClass = pageCompile.buildPage(conf.data)
  let vm = new PageClass()
  vm.$mount(conf.el)
}

export default main
export {
  EditorUtils,
  Editor
}



