import vdata, * as f from './vdata'
import * as utils from './utils'
import * as cp from './nodeCompile'
import * as cst from './const'
import * as wd from '@/assets/js/widget'
import cfg from './config'

function DynamicContext(tag, attr, children) {
  this.tag = tag
  this.attr = attr
  this.children = children
}

function validOpt(prop, value) {
  // if it is v_for element, it should always true for handling
  if (prop === cst.V_FOR) return true
  return prop && value
}

/*
generate html element attributes by options
only for editor page
 */
function handleEditorOptions(_vm, vNode, obRoot, genKey) {
  let options = vNode.options || []
  let attr = vdata.createAttr(vNode.attr.native())
  let tag = vNode.tag
  let children
  let skip = false
  options.forEach(item => {
    const prop = item.prop
    const value = item.value
    if (!validOpt(prop, value)) return
    if (prop === 'v-model') { // data bind
      calcPath(vNode, value, true)
    } else if (utils.propType(prop) === cst.V_BIND ||
      utils.propType(value) === cst.V_BIND) {
      calcPath(vNode, utils.getProps(value))
    } else if (prop === '$children') {
      children = handleChildrenOpt(item.type, value)
    } else if (prop === '$layout') {
      handleLayoutOpt(vNode, value, genKey)
    } else if (prop === cst.V_FOR) {
      skip = true // In editor mode skip v-for
    } else {
      handleCommonAttr(attr, prop, value)
    }
  })
  if (skip) return null
  if (!children) children = []
  handleNodeChanged(vNode)
  return new DynamicContext(tag, attr, children)
}

function handleNodeChanged(vNode) {
  if (vNode.changed != null && !vNode.changed) return
  let itemKey
  if (vNode.children.length > 0) itemKey = vNode.children[0].key
  if (itemKey && vNode.hasAttr('props', 'compKey')) {
    vNode.setAttr('props', {
      compKey: itemKey
    })
  }
  vNode.changed = false
}

/**
 * place tag from editor tag to real tag
 */
function replaceAndClean(vNode) {
  if (vNode.type === 'form-layout' || vNode.type === 'tiled-layout') {
    vNode.tag = 'wn-container'
    vNode.delAttr('props')
  } else if (vNode.type === 'editor-layout-col') {
    vNode.tag = stardComp['layout-col'].tag
    vNode.delAttr('props', 'nodeKey')
    vNode.delAttr('props', 'layout')
    vNode.delAttr('props', 'compKey')
  } else if (vNode.type === 'dialog') {
    vNode.tag = 'el-dialog'
  } else if (vNode.type === 'editor-item') {
    return
  } else if (vNode.type === 'editor-layout-row') {
    vNode.tag = stardComp['layout-row'].tag
    vNode.delAttr('props', 'layoutKey')
    vNode.delAttr('props', 'row')
    vNode.delAttr('props', 'data')
  } else if (vNode.type === 'dyn-comp') {
    let opt = wd.delOptByProp('name', vNode.options)
    if (!opt) return
    let name = opt.value
    vNode.tag = name
  }
  return vNode
}

/**
 * generate html element attributes by options
 * For preview page and real page
 * @param {*} vNode vNode with no handle option
 * @param {*} obData observer data
 * @param {*} methods store event listener
 */
function handleDynConext(vNode, obData) {
  if ((typeof vNode) === 'string') return vNode
  vNode = replaceAndClean(vNode) // replace editor props
  if (!vNode) return
  let options = vNode.options || []
  let children
  options.forEach(item => {
    const prop = item.prop
    const value = item.value
    if (!validOpt(prop, value)) return
    if (prop === 'v-model' || utils.propType(prop) === cst.V_BIND ||
      utils.propType(value) === cst.V_BIND) { // :props.xxxx one way bind
      let path = vNode.getDataPath(utils.getProps(value))
      // create observer data
      utils.createObjPath(item.type, obData, path)
      let bindProps = prop === 'v-model' ? ['value'] : utils.getProps(prop).split('.')
      // handle data bind
      cp.compileDataBind(vNode, path, bindProps)
    } else if (prop === '$children') {
      children = handleChildrenOpt(item.type, value)
    } else if (prop === cst.V_FOR) {
      handleVfor(vNode, obData, value, item.type)
    } else if (prop === cst.V_BIND) { // v-bind="xxx" for props pass
      utils.createObjPath(item.type, obData, value)
      vNode.setDirectives(vdata.createDirectives(cst.V_BIND, value))
    } else if (prop === '$events') { // @ event listener
      if (Array.isArray(value)) {
        value.forEach(item => {
          cp.compileEventListener(vNode, item.event, item.value)
        })
      }
    } else {
      handleCommonAttr(vNode.attr, prop, value)
    }
  })
  if (children == null) children = []
  vNode.children = []
  children.forEach(child => {
    let childNode = handleDynConext(child, obData)
    vNode.addChild(childNode)
  })
  return vNode
}

function handleCommonAttr(attr, prop, value) {
  if (prop === 'ref') {
    attr.set(prop, value)
    return
  }
  let splitKey = prop.split('.')
  if (splitKey.length === 1) splitKey.splice(0, 0, 'props')
  if (splitKey.length === 2) {
    const object = {}
    object[splitKey[1]] = value
    attr.set(splitKey[0], object)
  } else {
    throw new Error('not support props:' + prop)
  }
}

/**
 * calc v-for,create v-for element
 * @param {*} vNode
 * @param {*} value loop data reference
 */
function handleVfor(vNode, obData, value, type) {
  utils.createObjPath(type, obData, value)
  vNode.setDirectives(vdata.createDirectives(cst.V_FOR, value))
  const loopConf = stardComp[vNode.type][cst.V_FOR]
  // e.g. props{label:item.label,value:item.value}
  loopConf.forEach(prop => {
    cp.compileVforItem(vNode, prop)
  })
}

/**
 * component can add dynamic children by children options
 * @param {*} type  child tag
 * @param {*} value options with children data
 */
function handleChildrenOpt(type, value) {
  let childNode
  let children = []
  if (Array.isArray(value)) {
    value.forEach(child => {
      let compType = type || child.$el
      childNode = stardComp.create(compType)
      if (child.$children) childNode.addChild(child.$children)
      childNode.setAttr('props', child)
      children.push(childNode)
    })
  } else {
    childNode = value
    children.push(childNode)
  }
  return children
}

/**
 * handle layout vnode options
 * currently consider row/col layout
 * different with other options ,layout option will change vnode tree
 * so need flag to check if option was done or not
 * @param {*} value
 * @param {*} children
 */
function handleLayoutOpt(layoutNode, value, genKey) {
  if (value == null) value = ''
  const wrapperNode = layoutComp.getWrapperNode(layoutNode, genKey)
  const wrapperChildren = wrapperNode.children
  const rows = value
  let rowArray = []
  let colArray = []
  let rowDataArray = []
  rows.forEach((rowItem, row) => {
    let rowNode = wrapperChildren[row]
    if (!rowNode) {
      rowNode = stardComp.create('editor-layout-row')
      rowNode.key = genKey()
    }
    rowArray[row] = rowNode
    const rowChildren = rowNode.children
    colArray[row] = []
    rowDataArray[row] = []
    rowItem.forEach((colItem, col) => {
      const spanValue = Number(colItem.span)
      if (isNaN(spanValue) || spanValue <= 0) return
      let colNode = rowChildren[col]
      if (!colNode) {
        colNode = stardComp.create('editor-layout-col')
        colNode.key = genKey()
      }
      if ((layoutNode.changed == null || layoutNode.changed) ||
        (colNode.changed == null || colNode.changed)) {
        let itemKey = colNode.children.length > 0 ? colNode.children[0].key : null
        colNode.setAttr({
          nodeKey: colNode.key,
          layout: layoutNode.type,
          compKey: itemKey
        })
        colNode.setOptions([{
          prop: 'span',
          value: spanValue
        }])
        colNode.changed = false
      }
      rowDataArray[row].push(new f.EditorCol(colNode.key, spanValue))
      colArray[row].push(colNode)
    })
  })

  if (layoutNode.changed == null || layoutNode.changed) {
    wrapperNode.children = []
    rowArray.forEach((rowNode, row) => {
      rowNode.children = []
      rowNode.setAttr({
        layoutKey: layoutNode.key,
        row: row + 1,
        data: rowDataArray[row]
      })
      colArray[row].forEach(colNode => {
        rowNode.addChild(colNode)
      })
      wrapperNode.addChild(rowNode)
    })
    layoutNode.changed = false
  }
  if (colArray.length > 0) return colArray[colArray.length - 1][0]
}

var stardComp = cfg.getStardComp()
var formComp = cfg.getFormComp()

function validate(compName) {
  if (!compName) throw new Error('component name cannot be empty')
  if (!stardComp[compName]) throw new Error('cannot get component name:[' + compName + ']')
}
/**
 * create vnode if there is no specific component name
 * default is div element
 */
stardComp.createEmpty = function (genKey) {
  let vNode = vdata.createRoot({
    tag: 'div'
  })
  vNode.type = 'div'
  vNode.key = genKey()
  vNode.setAttr('class', {
    'el-page-default': true
  })
  return vNode
}

/**
 * Generate vnode data by component type
 */
stardComp.create = function (compName, genKey) {
  if (compName == null) {
    return this.createEmpty(genKey)
  }
  validate(compName)
  const compData = stardComp[compName]
  const vNode = vdata.createVnode(compData)
  vNode.type = compName
  if (genKey instanceof Function) {
    vNode.key = genKey()
  }
  const children = compData.children
  if (children != null) {
    children.forEach(child => {
      const childNode = this.create(child.el, genKey)
      vNode.addChild(childNode)
    }, this)
  }
  return vNode
}

var layoutComp = {}
/**
 * create layout node
 * change tag to main layout wrapper
 */
layoutComp.create = function (compName, genKey) {
  let layoutNode = stardComp.create(compName, genKey)
  let tag = layoutNode.tag
  layoutNode.tag = cst.MAIN_LAYOUT_NAME
  layoutNode.setAttr({
    layout: tag,
    nodeKey: layoutNode.key
  })
  return layoutNode
}

/**
 * handle different type layout
 * for form-layout ,return el-form element
 */
layoutComp.getWrapperNode = function (layoutNode, genKey) {
  const type = layoutNode.type
  if (type === 'form-layout') {
    if (layoutNode.children.length === 0) {
      let wrappedNode = stardComp.create('form', genKey)
      layoutNode.addChild(wrappedNode)
    }
    return layoutNode.children[0]
  }
  return layoutNode
}
layoutComp.createItem = function (layoutType, itemType, genKey) {
  let item = stardComp.create(itemType, genKey)
  // for wrap form-item
  if (layoutType === 'form-layout' && formComp.indexOf(itemType) >= 0) {
    item = createFormItem(item, genKey)
  } else if (layoutType === 'tiled-layout') {
    let editorItem = stardComp.create('editor-item', genKey)
    editorItem.setAttr({
      nodeKey: editorItem.key,
      compKey: item.key,
      isColumn: false,
      layout: layoutType
    })
    if (itemType === 'button') {
      editorItem.setAttr({
        isFull: false
      })
    }
    editorItem.addChild(item)
    item = editorItem
  }
  return item
}
/**
 * handle put item to gird layout directly,add new rows
 */
layoutComp.updateLayout = function (layoutNode, genKey, editInfo) {
  if (!editInfo) {
    return
  }
  let wrapperNode = this.getWrapperNode(layoutNode)
  let layoutValue = wd.getOptByProp(cst.LAYOUT_PROPS, layoutNode.options).value // get layout option value
  if (editInfo === cst.COPY_LAYOUT_LAST_ROW) {
    let lastRowData = layoutValue.length > 0 ? layoutValue[layoutValue.length - 1] : utils.createDefaultRow()
    layoutValue.push(utils.deepClone(lastRowData))
  } else if (editInfo.data === cst.DELETE_LAYOUT_ROW) {
    layoutValue.splice(editInfo.row, 1)
    wrapperNode.children.splice(editInfo.row, 1)
  } else {
    layoutValue[editInfo.row] = editInfo.data.row
    let rowNode = wrapperNode.children[editInfo.row]
    let children = []
    rowNode.children.forEach(colNode => {
      if (editInfo.data.remove.indexOf(colNode.key) < 0) {
        children.push(colNode)
      }
    })
    rowNode.children = []
    rowNode.addChild(children)
  }
  layoutNode.changed = true
  return handleLayoutOpt(layoutNode, layoutValue, genKey)
}

function createFormItem(item, genKey) {
  let formItem = stardComp.create('form-item', genKey)
  formItem.addChild(item)
  return formItem
}

/**
 * When child need to add observer data,it shoud add parent name
 * e.g. form  -> form item
 * @param {*} vNode
 */
function findFormObject(vNode) {
  if (vNode.type === 'form') {
    let options = vNode.options
    if (options == null) {
      options = []
    }
    let obDataName
    options.forEach(opt => {
      if (opt.prop === ':model') {
        obDataName = opt.value
      }
    })
    return obDataName
  } else {
    return vNode.parent == null ? null : findFormObject(vNode.parent)
  }
}
/**
 * calculate vNode path,specific for form-item
 * @param {*} vNode
 * @param {*} path
 */
function calcPath(vNode, path, findParent) {
  let pDataName
  if (findParent && vNode.parent && vNode.parent.type === 'form-item') {
    pDataName = findFormObject(vNode) // find form bind data
    vNode.parent.setAttr({
      'prop': path
    }) // set prop to form-item
  }
  const key = path
  if (pDataName == null) {
    vNode.addDataPath(key, path)
  } else {
    vNode.addDataPath(key, pDataName + '.' + path)
  }
  return vNode.getDataPath(key)
}

export function toEditVnode(data, genKey) {
  let vNode = vdata.createVnode(data)
  if (genKey) vNode.key = genKey()
  let props = vNode.getAttr('props')
  if (props && props.nodeKey) {
    props.nodeKey = vNode.key // update key
  }
  const children = data.children.map(child => {
    return toEditVnode(child, genKey)
  })
  vNode.addChild(children)
  return vNode
}

/**
 * Create vNode for render
 * @param {*} data  vnode data
 * @param {*} obData observer data,this function will inject obData by v-model
 */
export function toStaticVnode(data, obData) {
  let children = []
  data.children.forEach(child => {
    let node = toStaticVnode(child, obData)
    if (node) children.push(node)
  })
  let vNode = vdata.createVnode(data)
  let newNode = handleDynConext(vNode, obData)
  // if node is emtpy ,try to return child node(replace unused node)
  if (!newNode) return children.length > 0 ? children[0] : null
  newNode.addChild(children)
  return newNode
}

export {
  stardComp,
  layoutComp,
  handleEditorOptions
}



