 <template>
  <div>
    动态组件({{name}})
    <component :is="name"></component>
  </div>
</template>
<script>
export default {
  name: 'DynComp',
  props: {
    name: ''
  },
  computed: {
    hasName: function() {
      if (this.name) return true
      return false
    }
  }
}
</script>
<style scoped>
.empty-dyn-comp {
  padding: 10px;
}
</style>


