import {
  format
} from './utils'
import np, {
  env
} from './nodeCompile'
import Vue from 'vue'
import {
  toStaticVnode
} from './component'

let entryTpl = 'var pages=[];/*add*/(function(s){var r =[];s.forEach(function(p){r.push(p())});return r;})(pages)'
let comments = '/*{0}*/{1}/*{0}*/'
let inputType = 'props:e.props,created:e.created,mounted:e.mounted,methods:e.methods,watch:e.watch,computed:e.computed'
let pageTpl = `pages.push(function(){var exports;{0};var e=exports;return {name:'{1}',data:{2},${inputType},h:{3}}});`

function buildPageJs(pageInfo) {
  let result = getRenderCompile(pageInfo)
  let pageJs = format(pageTpl, pageInfo.script, pageInfo.id, result.data, result.h)
  return format(comments, pageInfo.id, pageJs)
}

function buildPage(pageInfo) {
  let pageJs = buildPageJs(pageInfo)
  return buildSinglePage(pageJs)[0]['data']
}

function buildSinglePage(pageJs) {
  let addPos = entryTpl.indexOf('/*add*/')
  pageJs = entryTpl.slice(0, addPos) + pageJs + entryTpl.slice(addPos, entryTpl.length)
  return compile(pageJs)
}

function getCompiledIds(mouduleJs) {
  /* eslint-disable no-eval */
  let pageData = eval(mouduleJs)
  return pageData.map(item => {
    return item.name
  })
}

function compile(mouduleJs) {
  /* eslint-disable no-eval */
  let pageData = eval(mouduleJs)
  let res = []
  pageData.forEach(item => {
    let data = {}
    let props = item.props || {}
    for (let key in item.data) {
      if (!props.hasOwnProperty(key)) data[key] = item.data[key]
    }
    let info = {
      name: item.name,
      data() {
        return data
      },
      render(h) {
        return item.h.call(this, h, env)
      },
      methods: item.methods,
      created: item.created,
      mounted: item.mounted,
      beforeUpdate: item.beforeUpdate,
      props: item.props,
      watch: item.watch,
      computed: item.computed
    }
    res.push({
      name: info.name,
      data: Vue.extend(info)
    })
  })
  return res
}

function getRenderCompile(pageInfo) {
  let domData = pageInfo.data
  if (!domData) return
  if (typeof domData === 'string') domData = JSON.parse(domData)
  if (!(domData instanceof Object)) throw new Error('not supported domData type:' + domData)

  let obData = {}
  const renderTree = toStaticVnode(domData, obData)
  let f = function (k, v) {
    return v === undefined ? null : v
  }
  return {
    data: JSON.stringify(obData, f),
    h: np.cpRenderFunstr(renderTree)
  }
}

export default {
  buildPageJs,
  buildPage,
  compile,
  entryTpl,
  buildSinglePage,
  getCompiledIds
}



