export default {
    name: 'default',
    mounted: function () {
        this.test = { TXM: 'txm', PATNAME: '123', selectData: [] }
        this.title = '123'
        this.tt2 = [{ value: '1' }, { value: '2' }]
    },
    methods: {
        close: function ($event, form) {
            console.log('close')
            this.$visible = false
        },
        reasonChange() {
            console.log('reasonChange')
            this.test.selectData = [{ NAME: '11' }, { NAME: '2222' }]
        },
        save: function (form) {
            console.log(form)
        }
    },
    watch: {
        visible(val) {
            this.$emit('update:visible', val)
        },
        $attrs(val) {
            console.log(val)
            Object.keys(val).forEach(key => {
                this[key] = val[key]
            }, this)
        }
    }

}




