<template>
    <div>
        <slot></slot>
        <zone-item class="item-zone-empty" :nodeKey="nodeKey" layout="new-row"></zone-item>
    </div>
</template>
<script>
import ZoneItem from './ZoneItem'
export default {
  name: 'GridLayout',
  props: {
    nodeKey: null
  },
  components: {
    'zone-item': ZoneItem
  }
}
</script>
