<template>
    <div>
        <zone-item class="editor-layout-zone" :nodekey="nodeKey" action="up"></zone-item>
        <wn-container :title="title">
              <div slot="header" :class="['layout-click-zone',layoutClass]" @click.stop="click" @mouseenter="mouseEnter=true" @mouseleave="mouseEnter=false">
                <span v-if="mouseEnter||focus">设置</span>
                <i class="el-icon-delete editor-button editor-icon-delete" title="删除" v-if="mouseEnter" @click.stop="deleteLayout"></i>
            </div>
         <component :is="layout" :nodeKey="nodeKey" >
            <slot></slot>
        </component>
         </wn-container>
        <zone-item class="editor-layout-zone" :nodekey="nodeKey" action="down"></zone-item>
    </div>
</template>
<script>
import { EditorUtils } from '../../assets/js/winning-editor'
import GridLayout from './GridLayout'
import TiledLayout from './TiledLayout'
import ZoneItem from './ZoneItem'

export default {
  name: 'editorLayout',
  components: {
    'zone-item': ZoneItem, // dragable item to  zone
    'grid-layout': GridLayout,
    'tiled-layout': TiledLayout
  },
  props: {
    nodeKey: {
      // layout key
      type: Number,
      required: true
    },
    layout: {
      // grid-layout,tiled-layout
      type: String,
      required: true
    },
    title: ''
  },
  data() {
    return {
      mouseEnter: false,
      focus: false,
      showDelete: true
    }
  },
  mounted: function() {
    EditorUtils.onItemClick(this.clickListener)
  },
  methods: {
    click: function() {
      this.focus = true
      EditorUtils.emitItemClick(this.nodeKey)
    },
    clickListener: function(nodeKey) {
      this.nodeKey !== nodeKey ? (this.focus = false) : (this.focus = true)
    },
    deleteLayout: function() {
      this.$Editor.deleteConfirm(() => {
        EditorUtils.emitItemDelete(this.nodeKey)
      }, '是否删除布局？')
    }
  },
  computed: {
    layoutClass: function() {
      if (!this.mouseEnter && !this.focus) {
        return ''
      } else if (this.focus) {
        return 'layout-focus'
      } else {
        return 'layout-mouse-over'
      }
    },
    settingMessage: function() {}
  }
}
</script>
<style scoped>
.layout-click-zone {
  width: 60px;
  height: 5px;
  position: relative;
}

.layout-mouse-over {
  border: 1px solid blue;
  height: 30px;
}

.layout-focus {
  border: 1px solid red;
  height: 30px;
}
</style>



