import {
  deepClone
} from './utils'
const vBuilder = {
  createVnode: function (prop, children) {
    return new Vnode(prop, children)
  },
  createRoot: function (prop, children) {
    return new Vnode(prop, children)
  },
  createAttr: function (source) {
    return new Attribute(source)
  },
  createPageInfo: function (id, data, script, conf) {
    return new PageInfo(id, data, script, conf)
  },
  createDirectives: function (key, value) {
    return new Directives(key, value)
  }
}

function Vnode(prop, children) {
  if (prop == null) {
    prop = {}
  }
  this.children = []
  this.addChild(children)
  this.tag = prop.tag
  if (prop.attr instanceof Attribute) {
    this.attr = prop.attr
  } else {
    this.attr = new Attribute(prop.attr)
  }
  this.type = prop.type // input,form .etc
  this.key = prop.key
  this.defaultValue = prop.defaultValue
  // dynamic content
  if (Array.isArray(prop.options)) {
    this.options = deepClone(prop.options)
  } else {
    this.options = []
  }
  this.setDataPath(prop.dataPath) // store observer data path
  this.slot = prop.slot // slot name,this vnode will be inject in slot as child
  this.dataType = prop.dataType // define data type if vnode need to bind data
}

Vnode.prototype.addChild = function (children, pos) {
  if (Array.isArray(children)) {
    children.forEach(child => {
      child.parent = this
      this.children.push(child)
    }, this)
  } else if (children instanceof Vnode) {
    children.parent = this
    pos == null ? this.children.push(children) : this.children.splice(pos, 0, children)
  } else if ((typeof children) === 'string') {
    this.children.push(children)
  }
}

Vnode.prototype.setAttr = function (key, value) {
  if (arguments.length === 1) {
    this.attr.set('props', arguments[0])
  } else {
    this.attr.set(key, value)
  }
}

Vnode.prototype.getAttr = function (key, value) {
  return this.attr.get(key, value)
}

Vnode.prototype.delAttr = function (key, value) {
  this.attr.del(key, value)
}

Vnode.prototype.hasAttr = function (key, value) {
  return this.attr.contains(key, value)
}

Vnode.prototype.setOptions = function (options) {
  let input = []
  options.forEach(item => {
    input.push(item.prop)
  })
  this.options = this.options.filter(item => {
    return input.indexOf(item.prop) < 0
  })
  this.options = this.options.concat(options)
  this.changed = true
}

Vnode.prototype.toData = function () {
  let json = {}
  json.tag = this.tag
  json.attr = this.attr.native()
  json.type = this.type
  json.defaultValue = this.defaultValue
  json.options = this.options
  json.dataType = this.dataType
  json.dataPath = this.dataPath
  json.slot = this.slot
  json.children = this.children.map(child => {
    return child.toData()
  })
  return json
}
/**
 * sometimes data path  need to be calcauted ,dataPath is for saving this value
 */
Vnode.prototype.setDataPath = function (dataPath) {
  this.dataPath = dataPath
}

Vnode.prototype.addDataPath = function (key, path) {
  if (this.dataPath == null) {
    this.dataPath = {}
  }
  this.dataPath[key] = path
}

Vnode.prototype.getDataPath = function (key) {
  return this.dataPath == null ? null : this.dataPath[key]
}

Vnode.prototype.setDirectives = function (directives) {
  this.directives = directives
}

Vnode.prototype.getDirectives = function () {
  return this.directives
}

function Attribute(source) {
  if (source == null) {
    this.source = {}
  } else {
    this.source = deepClone(source)
  }
}

Attribute.prototype.native = function () {
  var target = Object.assign({}, this.source)
  return target
}

Attribute.prototype.set = function (attr, value) {
  if (value == null) return
  if (!this.source[attr]) this.source[attr] = {}
  if (value instanceof Object) {
    for (let key in value) {
      if (key.charAt(0) !== '$') this.source[attr][key] = value[key]
    }
  } else {
    this.source[attr] = value
  }
}

Attribute.prototype.get = function (attr, value) {
  let res = this.source[attr]
  if (value && res) res = res[value]
  return res
}

Attribute.prototype.del = function (attr, value) {
  if (!attr) return
  if (value) {
    delete this.source[attr][value]
  } else {
    delete this.source[attr]
  }
}

Attribute.prototype.contains = function (attr, value) {
  if (!attr) return false
  if (!this.source.hasOwnProperty(attr)) return false
  if (!value) return true
  let temp = this.source[attr]
  return temp.hasOwnProperty(value)
}

function PageInfo(id, data, script, conf) {
  this.id = id
  this.data = data
  this.script = script
  this.conf = conf
}

function Directives(key, value) {
  this.key = key
  this.value = value
}

export function EditorCol(key, span, xs, sm, md) {
  this.key = key
  this.span = span
  this.xs = xs
  this.sm = sm
  this.md = md
}

export function LayoutEditInfo(layoutKey, row, data) {
  this.layoutKey = layoutKey
  this.row = row
  this.data = data
}

export default vBuilder



