import interact from 'interactjs'
/**
 * default move action
 * @param {*} event
 */
export function dragMoveListener(event) {
    var target = event.target
    var x = (parseFloat(target.getAttribute('data-x')) || 0) + event.dx
    var y = (parseFloat(target.getAttribute('data-y')) || 0) + event.dy
    target.style.webkitTransform =
        target.style.transform =
        'translate(' + x + 'px, ' + y + 'px)'
    target.setAttribute('data-x', x)
    target.setAttribute('data-y', y)
}

/**
 * init drag component instance
 */
export function initDragItem(selector, manualStart) {
    let ineract = interact(selector).draggable({
        inertia: true,
        autoScroll: true,
        manualStart: manualStart,
        onstart: function (event) {
            // eventBus.$emit(dragStartEvent, event)
        }
    })
    return ineract
}
/**
* init drag component to zone instance
*/
export function initDragZone(selector, acceptSelector) {
    return interact(selector).dropzone({
        accept: acceptSelector,
        overlap: 0.1
    })
}

/**
 * init drag defined component
 */
export function initCompDrag() {
    let interact = initDragItem('.layout-draggable,.item-draggable', true)
    interact.on('dragend', function (event) {
        let target = event.target
        target.parentNode.removeChild(target)
    })
    interact.on('dragmove', dragMoveListener)
    interact.on('move', function (event) {
        let interaction = event.interaction
        if (interaction.pointerIsDown && !interaction.interacting()) {
            let clone = event.currentTarget.cloneNode(true)
            clone.classList.add('clone-item')
            event.currentTarget.parentNode.appendChild(clone)
            interaction.start({ name: 'drag' },
                event.interactable,
                clone)
        }
    })
    return interact
}

export function initEditorItemDrag(dragMoveListener) {
    let interact = initDragItem('.editor-item-move', true)
    interact.on('dragmove', dragMoveListener)
    interact.on('move', function (event) {
        let interaction = event.interaction
        if (interaction.pointerIsDown && !interaction.interacting()) {
            let clone = event.currentTarget.cloneNode(false)
            event.currentTarget.style.opacity = 0.5
            event.currentTarget.style.background = '#000'
            event.currentTarget.appendChild(clone)
            clone.style.position = 'absolute'
            clone.style.top = event.offsetY + 'px'
            clone.style.left = event.offsetX + 'px'
            clone.style.width = '50px'
            clone.appendChild(document.createTextNode('移动控件'))
            interaction.start({ name: 'drag' },
                event.interactable,
                clone)
        }
    })
    interact.on('dragend', function (event) {
        let target = event.target
        target.parentNode.style.opacity = ''
        target.parentNode.style.background = ''
        target.parentNode.removeChild(target)
    })
    return interact
}



