 <template>
    <div :class="['wn-container-default',showHeader?'wn-container-border':'wn-con-border-custom']">
        <div :class="[showHeader?'wn-con-heading-default':'wn-con-heading-custom']">
          <slot name="header"></slot>
           <span>{{title}}</span>
        </div>
        <div :class="[showHeader?'wn-container-content':'wn-con-content-custom']">
       <slot></slot>
        </div>
    </div>
</template>
<script>
export default {
  name: 'WnContainer',
  props: {
    title: ''
  },
  computed: {
    showHeader: function() {
      if (this.title) return true
      return false
    }
  }
}
</script>
<style scoped>

</style>


