import { handleEditorOptions } from '../assets/js/component'
import EditorLayout from './layout/EditorLayout'
import TiledLayout from './layout/TiledLayout'
import UIEditorItem from './EditorItem'
import EditorDialogContainer from './container/DialogContainer'
import EditorRow from './layout/EditorLayoutRow'
import DynComp from '@/components/DynComp'

function handleSpecifcProps(vNode, attribute) {
    if (vNode.type === 'select') {
        attribute.set('props', { value: '' })
    }
}

export default {
    name: 'EditorDynView',
    components: {
        'editor-layout': EditorLayout, // editor-layout
        'tiled-layout': TiledLayout, // tiled-layout
        'ui-editor-item': UIEditorItem,  // dragable item zone,el-col wrapper
        'editor-dialog-container': EditorDialogContainer, // editor container for dialog,
        'editor-row': EditorRow, // el-row wrapper
        'dyn-comp': DynComp // el-row wrapper
    },
    props: {
        vNode: {
            type: Object,
            default: function () {
                return null
            }
        }
    },
    data() {
        return {
            data: {}
        }
    },
    render(h) {
        if (!this.vNode) return
        return this.createElement(h, this.vNode)
    },
    mounted: function () {
    },
    methods: {
        createElement: function (h, vNode) {
            if (vNode == null) throw new Error('vNode can\'t be null')
            if ((typeof vNode) === 'string') return vNode
            const dynContext = handleEditorOptions(this, vNode, this.data, this.vNode.genKey)
            if (!dynContext) return
            const newChildren = vNode.children.concat(dynContext.children)

            let children = newChildren.map(function (child) {
                return this.createElement(h, child)
            }, this)
            handleSpecifcProps(vNode, dynContext.attr)
            return h(dynContext.tag, dynContext.attr.native(), children)
        }
    }
}



