<template>
    <el-row>
        <div class="editor-row-setting" @mouseenter="mouseEnter=true" @mouseleave="mouseEnter=false">
            <el-popover trigger="click" v-if="showEdit" :value="true">
                <span class="editor-popover-title">行号:{{row}}</span>
                <el-table border :data="rowData" @row-click="test(layoutKey,$event,$event)">
                    <el-table-column type="index" width="60" label="序号" >
                    </el-table-column>
                    <el-table-column width="90" label="栅格列数" class-name="editor-table-col">
                        <template slot-scope="scope">
                            <el-input :value="scope.row.span" @input="valueInput(scope.row,'span',$event)"></el-input>
                        </template>
                    </el-table-column>
                    <el-table-column width="60" label="操作">
                        <template slot-scope="scope">
                            <i class="el-icon-delete editor-button editor-icon-delete" title="删除" @click="deleteCol(scope.$index,scope.row)"></i>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="editor-footer">
                    <div class="inline-editor-left">
                        <el-button size="small" type="primary" @click="addCol">新增列</el-button>
                    </div>
                    <div class="inline-editor-right">
                        <el-button size="small" type="primary" @click="cancel">取消</el-button>
                        <el-button size="small" type="primary" @click="confirm">确定</el-button>
                    </div>
                </div>
            </el-popover>
            <div v-if="mouseEnter">
                <i class="el-icon-setting editor-button" title="行编辑" @click="edit"></i>
                <i class="el-icon-delete editor-button editor-icon-delete" title="行删除" @click="deleteRow"></i>
            </div>
        </div>
        <slot></slot>
    </el-row>
</template>
<script>
import { EditorUtils } from '@/assets/js/winning-editor'
import * as f from '@/assets/js/vdata'
import * as cst from '@/assets/js/const'

export default {
  name: 'EditorRow',
  props: {
    layoutKey: {
      type: Number,
      required: true
    },
    row: {
      type: Number,
      required: true
    },
    data: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      mouseEnter: false,
      showEdit: false,
      removeList: [],
      rowData: []
    }
  },
  methods: {
    test: function(row, event, column) {
      console.log(row, event, column)
    },
    edit: function() {
      this.showEdit = true
      this.rowData = Object.assign([], this.data)
    },
    deleteRow: function() {
      this.$Editor.deleteConfirm(() => {
        let editInfo = new f.LayoutEditInfo(
          this.layoutKey,
          this.row - 1,
          cst.DELETE_LAYOUT_ROW
        )
        EditorUtils.emitLayoutChange(editInfo)
      }, '是否删除行？')
    },
    valueInput: function(row, attr, value) {
      row[attr] = Number(value)
    },
    addCol: function() {
      this.rowData.push({ span: null })
    },
    deleteCol: function(index, row) {
      this.rowData.splice(index, 1)
      if (row.key) {
        this.removeList.push(row.key)
      }
    },
    cancel: function() {
      this.done()
    },
    confirm: function() {
      if (!this.validate()) {
        return
      }
      let rowInfo = { remove: this.removeList, row: this.rowData }
      let editInfo = new f.LayoutEditInfo(this.layoutKey, this.row - 1, rowInfo)
      EditorUtils.emitLayoutChange(editInfo)
      this.done()
    },
    done: function() {
      this.removeList = []
      this.showEdit = false
      this.mouseEnter = false
      this.rowData = []
    },
    validate: function() {
      let spanValue
      let sum = 0
      for (let index in this.rowData) {
        spanValue = this.rowData[index].span
        if (isNaN(spanValue) || spanValue <= 0) {
          this.$Editor.message('warning', '栅格列数必须是数字并且大于0')
          return false
        }
        sum = sum + spanValue
      }
      if (sum > 24) {
        this.$Editor.message('warning', '栅格列数总数不能大于24')
        return false
      }
      return true
    }
  }
}
</script>
<style scoped>

</style>



