<template>
  <div class="editor-header" id="header">
    <div class="editor-header-left">
      <img src="../assets/images/logo.png" alt="logo" />
      <span>View Builder </span>
    </div>
    <div class="editor-header-center">
      <div>
        <dl>
          <dt>
            <i class="el-icon-document"></i>
          </dt>
          <dd>文本</dd>
        </dl>
        <dl>
          <dt>
            <i class="el-icon-picture"></i>
          </dt>
          <dd>图片</dd>
        </dl>
        <dl>
          <dt>
            <i class="el-icon-view"></i>
          </dt>
          <dd>视频</dd>
        </dl>
        <dl>
          <dt>
            <i class="el-icon-setting"></i>
          </dt>
          <dd>形状</dd>
        </dl>
        <dl>
          <dt>
            <i class="el-icon-menu"></i>
          </dt>
          <dd>扩展</dd>
        </dl>
      </div>
    </div>
    <div class="editor-header-right" v-if="true">
      <el-button type="success" size="small" @click="onSwitch">切换</el-button>
    </div>
  </div>
</template>

<script type="text/javascript">
export default {
  name: 'editor-header',
  methods: {
    onSwitch: function() {
      this.$emit('switch')
    }
  }
}
</script>

<style scoped>
.editor-header {
  height: 50px;
  background-color: #364e6a;
  line-height: 50px;
  color: #fff;
  width: 100%;
}

.editor-header .editor-header-left {
  display: inline-block;
  width: 250px;
}

.editor-header .editor-header-left img {
  vertical-align: sub;
  margin: 0 10px;
}

.editor-header .editor-header-center {
  display: inline-block;
  position: absolute;
  font-size: 12px;
  font-family: "Microsoft YaHei";
  left: 250px;
  right: 400px;
}

.editor-header .editor-header-center div {
  margin: auto;
  position: absolute;
  left: 50%;
  margin-left: -165px;
}

.editor-header .editor-header-center dl {
  float: left;
  margin: -8px 21px;
  text-align: center;
}

.editor-header .editor-header-center dl dt {
  height: 17px;
}

.editor-header .editor-header-center dl dd {
  margin-left: 0;
}

.editor-header .editor-header-right {
  display: inline-block;
  position: absolute;
  right: 10px;
  width: 400px;
  text-align: right
}
</style>



