import compAttrs from '@/assets/json/baseCompAttrs.json'
import pagePool from '@/assets/js/pagePool'
import * as cst from '@/assets/js/const'
import cfg from '@/assets/js/config'

/**
 * vnode option
 * @param {*} prop elemnt attribute name
 * @param {*} value atribute value
 */
export function Option(prop, value, type, mode) {
    this.prop = prop
    this.value = value
    if (type) this.type = type
    this.mode = mode
}

Option.prototype.getValue = function () {
    return this.value
}

export function getOptByProp(prop, opts) {
    let res = findOpt(prop, opts)
    if (res) return res.val
}

export function delOptByProp(prop, opts) {
    let res = findOpt(prop, opts)
    if (res) {
        opts.splice(res.index, 1)
        return res.val
    }
}

function findOpt(prop, opts) {
    if (!Array.isArray(opts)) return
    for (let index in opts) {
        if (opts[index].prop === prop) return {index: index, val: opts[index]}
    }
}

export function widgetRender(item, opt, h) {
    if (item.widget === 'input') {
        return this.renderInput(item, opt, h)
    } else if (item.widget === 'select') {
        return this.renderSelect(item, opt, h)
    } else if (item.widget === 'list') {
        return this.renderList(item, opt, h)
    } else if (item.widget === 'events') {
        return this.renderEvents(item, opt, h)
    } else if (item.widget === 'checkbox') {
        return this.renderCheckBox(item, opt, h)
    } else if (item.widget === 'v-for') {
        return this.renderLoop(item, opt, h)
    }
}

/**
 * render attribute container
 * @param {*} h render function
 * @param {*} attrs all widgets
 */
export function renderContainer(h, attrs) {
    if (this.show) {
        return (<div>
            <div class="widget-cate">属性</div>
            <div style="padding:5px">
                <el-form label-width="80px" label-position="left">
                    {attrs}
                </el-form>
            </div>
        </div >)
    }
}

export function renderInput(item, option, h) {
    const value = option.getValue ? option.getValue() : option.value
    const onInput = (newValue) => { this.updateOptions(option, newValue, item) }
    return (<el-form-item label={item.label}>
        <el-input size="small" value={value} onInput={onInput}></el-input>
    </el-form-item>)
}

export function renderSelect(item, option, h) {
    const value = option.getValue ? option.getValue() : option.value
    const selOpts = cfg.getSelect(item.select, this.pageId).map(item => {
        return (<el-option value={item}>
        </el-option>)
    })
    const onInput = (newValue) => { this.updateOptions(option, newValue, item) }
    return (<el-form-item label={item.label}>
        <el-select id="event-select" value={value} onInput={onInput} clearable>
            {selOpts}
        </el-select>
    </el-form-item>)
}

export function renderList(item, option, h) {
    let optList = option.value
    if (!Array.isArray(optList)) optList = []
    let list = optList.map((data, index) => {
        // check el
        if (data.el !== item.el) {
            throw new Error('Error for list widget,el value not match:' + item.el)
        }
        // list support render other widget by el
        const widgetsRender = this.renderListItem(data.el, data.options, h)

        const deleteClick = () => { this.listDelete(optList, index) }
        return (<div style="border: solid white;padding:5px">
            <el-button size="small" type="danger" onClick={deleteClick}>删除</el-button>
            {widgetsRender}</div>)
    })
    const addClick = () => { this.listAdd(optList, item) }
    return (<div style="height:100%"><p>{item.label}
        <el-button style="position: absolute; right: 20px" size="small" type="success" onClick={addClick}>
            新增</el-button>
    </p>{list}</div>)
}

export function renderListItem(el, itemOpt, h) {
    const listWidgets = compAttrs[el]
    const widgetsRender = listWidgets.map(item => {
        let opt = getOptByProp(item.prop, itemOpt)
        if (opt == null) {
            opt = new Option(item.prop, item.defaultValue, item.lazy)
            itemOpt.push(opt)
        }
        return this.widgetRender(item, opt, h)
    })
    return widgetsRender
}

function EventOpt(opt) {
    if (!opt) opt = {}
    this.event = opt.event || ''
    this.value = opt.value || ''
}
/**
 * render methods, get methods from logic module
 * @param {*} item
 * @param {*} option
 * @param {*} h
 */
export function renderEvents(item, option, h) {
    let moduleName = cst.TEMP_MODULE_NAME
    let eventList = cfg.getEventList(item.event)
    let paramMap = {}
    const EventOpts = eventList.map(item => {
        let value = item.type + item.event
        paramMap[value] = item.param
        return (<el-option value={value} label={item.event}>
            <span style="float: left;font-weight: bold;">{item.event}</span>
            <span style="float: right;color: #B38420">({item.desc})</span>
        </el-option>)
    })
    const addEvents = () => {
        this.events.data.push(new EventOpt())
    }
    const onEventInput = (item, newValue) => { item.event = newValue.trim() }
    const onValueInput = (item, newValue) => { item.value = newValue }
    const deleteEvent = (index) => { this.events.data.splice(index, 1) }
    const methodSelect = (query, cb) => {
        let methods = pagePool.getModuleMethods(moduleName)
        if (query) {
            methods = methods.filter(item => {
                return item.toLowerCase()
                    .indexOf(query.toLowerCase()) > -1
            })
        } else {
            methods = []
        }
        let results = methods.map(method => {
            return { 'value': method }
        })
        cb(results)
    }
    const showEvents = () => {
        let value = option.getValue()
        this.events.data = []
        if (Array.isArray(value) && value.length > 0) {
            value.forEach(opt => {
                this.events.data.push(new EventOpt(opt))
            })
        } else {
            this.events.data = [new EventOpt()]
        }
        this.events.show = true
    }
    const cancel = () => {
        this.events.show = false
    }
    const confirm = () => {
        let validate = true
        for (let index in this.events.data) {
            let e = this.events.data[index]
            if (!e.event || !e.value) {
                this.$message({
                    message: cst.EVENTS_CHECK_ERROR,
                    type: 'error'
                })
                validate = false
                break
            }
        }
        if (validate) {
            this.updateOptions(option, this.events.data, item)
            this.events.show = false
        }
    }
    let events = this.events.data.map((item, index) => {
        let methodPH = paramMap[item.event] ? '函数(' + paramMap[item.event] + ')' : '事件函数'
        return (<div style="width:100%; margin:5px">
            <el-select id="event-select" value={item.event} onInput={onEventInput.bind(this, item)}
            placeholder="事件名称" filterable allow-create>
                {EventOpts}
            </el-select>
            <el-autocomplete id="method-select" value={item.value} onInput={onValueInput.bind(this, item)}
            placeholder={methodPH} fetch-suggestions={methodSelect}>
            </el-autocomplete>
            <el-button class="el-icon-delete editor-icon-delete" onClick={deleteEvent.bind(this, index)}></el-button>
        </div>)
    })
    let eventNumber = Array.isArray(option.getValue()) ? option.getValue().length : 0
    let eventSpan = (<span style="margin-left:14px">事件数({eventNumber})</span>)
    return (
        <div class="editor-attr-event editor-attr-pop">
            <el-popover
                trigger="click"
                width="430"
                value={this.events.show}>
                <span class="editor-popover-title">事件列表</span>
                {events}
                <div class="editor-footer">
                    <div class="inline-editor-left">
                        <el-button size="mini" type="primary" onClick={addEvents}>新增事件</el-button>
                    </div>
                    <div class="inline-editor-right">
                        <el-button size="mini" type="primary" onClick={cancel}>取消</el-button>
                        <el-button type="primary" size="mini" onClick={confirm}>确定</el-button>
                    </div>
                </div>
            </el-popover>
            <el-button onClick={showEvents} size="small" class="edit-main-button">事件定义</el-button>
            {eventSpan}
        </div>)
}

export function renderCheckBox(item, option, h) {
    let value = option.getValue ? option.getValue() : option.value
    const onInput = (newValue) => {
        if (option.mode === 1 && newValue.charAt(0) !== ':') newValue = ':' + newValue
        this.updateOptions(option, newValue, item)
    }
    const click = () => {
        option.mode === 1 ? option.mode = null : option.mode = 1
        option.value = null
    }
    let component = option.mode === 1 ? (<el-form-item label={item.label} class="attr-switch-input">
        <el-input size="small" value={value} onInput={onInput}></el-input>
    </el-form-item>) : <el-checkbox class="editor-attr-checkbox" value={value} onInput={onInput}>{item.label}
        </el-checkbox>
    return (<div>
        {component}
        <i class="el-icon-information attr-switch" title="模式切换" onClick={click}></i>
    </div>)
}
function createEditorCell(h, prop) {
    const input = (row, attr, value) => {
        row[attr] = value
    }
    return {
        default: function (scope) {
            let value = scope.row[prop]
            return (<el-input value={value} onInput={input.bind(this, scope.row, prop)}></el-input>)
        }
    }
}
function ColOpt(opt) {
    if (!opt)opt = {}
    this.label = opt.label || ''
    this.prop = opt.prop || ''
    this.width = opt.width || ''
    this['min-width'] = opt['min-width'] || ''
}
export function renderLoop(item, option, h) {
    if (!this.temp[item.type]) {
        this.$set(this.temp, item.type, {data: [], show: false})
    }
    let bind = this.temp[item.type]
    const show = () => {
        bind.data = []
        let value = option.getValue()
        if (Array.isArray(value) && value.length > 0) {
            value.forEach(opt => {
                bind.data.push(new ColOpt(opt))
            })
        }
        bind.show = true
    }
    const addRow = () => {
        bind.data.push(new ColOpt())
    }
    const delCol = (index, row) => {
        bind.data.splice(index, 1)
    }
    const confirm = () => {
        this.updateOptions(option, bind.data, item)
        bind.show = false
    }
    const cancel = () => {
        bind.show = false
    }
    const tableColumn = item.data.map(col => {
        return (<el-table-column width="90" label={col.label} class-name="editor-table-col"
            scopedSlots={createEditorCell(h, col.prop)}>
        </el-table-column>)
    })
    return (<div class="editor-attr-pop editor-attr-button">
          <el-popover trigger="click" value={bind.show}>
          <el-table border data={bind.data} max-height="320">
              <el-table-column type="index" width="60" label="序号">
              </el-table-column>
              {tableColumn}
              <el-table-column width="60" label="操作"
              scopedSlots={{
                default: function (scope) {
                    return (<i class="el-icon-delete editor-button editor-icon-delete" title="删除" onClick={delCol.bind(this, scope.$index, scope.row)}></i>)
                 }
                 }}>
              </el-table-column>
          </el-table>
          <div class="editor-footer">
              <div class="inline-editor-left">
                  <el-button size="small" type="primary" onClick={addRow}>新增列</el-button>
              </div>
              <div class="inline-editor-right">
                  <el-button size="small" type="primary" onClick={cancel}>取消</el-button>
                  <el-button size="small" type="primary" onClick={confirm}>确定</el-button>
              </div>
          </div>
      </el-popover>
        <el-button onClick={show} size="small" class="edit-main-button">{item.label}</el-button>
    </div>)
}



