<template>
  <el-col v-if="isColumn" :span="span" @click.native.stop="click" :class="['editor-item',itemClass,dragCls]" @mouseenter.native="mouseEnter=true" @mouseleave.native="mouseEnter=false" :nodekey="nodeKey" :layout="layout">
    <slot>
    </slot>
    <div class="editor-item-footer" v-if="showDelete">
      <i class="el-icon-delete editor-button editor-icon-delete" title="删除" @click.stop="deleteItem(compKey)"></i>
    </div>
  </el-col>
  <div v-else @click.stop="click"  :class="['editor-item',itemClass,focusCls,dragCls,displayCls]" @mouseenter="mouseEnter=true" @mouseleave="mouseEnter=false" :nodeKey="nodeKey" :layout="layout">
    <slot>
    </slot>
    <div class="editor-item-footer" v-if="showDelete">
      <i class="el-icon-delete editor-button editor-icon-delete" title="删除" @click.stop="deleteItem(nodeKey)"></i>
    </div>
  </div>
</template>
<script>
import { EditorUtils } from '../assets/js/winning-editor'

export default {
  name: 'EditorItem',
  props: {
    span: {
      type: Number
    },
    nodeKey: {
      // editor-item key
      type: Number,
      required: true
    },
    compKey: {
      // render component key
      type: Number
    },
    layout: {
      type: String
    },
    isColumn: {
      type: Boolean,
      default: true
    },
    isFull: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      mouseEnter: false,
      focus: false,
      empty: this.compKey
    }
  },
  mounted: function() {
    EditorUtils.onItemClick(this.clickListener)
  },
  methods: {
    click: function() {
      this.focus = true
      EditorUtils.emitItemClick(this.compKey)
    },
    clickListener: function(compKey) {
      if (this.compKey !== compKey) {
        this.focus = false
      } else if (this.compKey != null) {
        this.focus = true
      }
    },
    deleteItem: function(key) {
      EditorUtils.emitItemDelete(key, this.compKey) // delete key,attribute key
    }
  },
  computed: {
    itemClass: function() {
      let itemClass
      if (this.compKey == null) {
        itemClass = 'item-zone'
      } else if (!this.mouseEnter && !this.focus) {
        itemClass = ''
      } else if (this.focus) {
        itemClass = 'item-mouse-focus-over'
      } else if (this.mouseEnter) {
        itemClass = 'item-mouse-over'
      }
      return itemClass
    },
    focusCls: function() {
      if (this.mouseEnter || this.focus) {
        return 'editor-item-wrap-enter'
      }
    },
    dragCls: function() {
      return this.compKey == null ? '' : 'editor-item-move'
    },
    displayCls: function() {
      if (!this.isFull) return 'editor-inline'
    },
    showDelete: function() {
      if (this.compKey != null && this.mouseEnter) {
        return true
      }
      return false
    }
  }
}
</script>
<style scoped>
.editor-item {
  position: relative;
}

.start-move {
  opacity: 0.5;
  background: #000;
}

.item-mouse-over {
  border: solid 2px blue;
}

.item-mouse-focus-over {
  border: solid 2px red;
}

.item-focus {
  border-bottom: solid 2px red;
}

.editor-item-wrap-enter {
  padding: 20px;
}
.editor-inline {
  display: inline-block;
}
</style>



