import {
  EditorUtils
} from '../assets/js/winning-editor'
import {
  deepClone
} from '../assets/js/utils'
import * as wd from '../assets/js/widget'

export default {
  name: 'CompAttrView',
  props: {
    pageId: null
  },
  data() {
    return {
      options: {},
      widgets: [],
      show: true,
      change: false,
      events: {
        show: false,
        data: []
      },
      temp: {}
    }
  },
  render(h) {
    let attrs
    if (Array.isArray(this.widgets)) {
      attrs = this.drawWidgets(h)
    }
    return this.renderContainer(h, attrs)
  },
  methods: {
    widgetRender: wd.widgetRender,
    renderContainer: wd.renderContainer,
    renderInput: wd.renderInput,
    renderList: wd.renderList,
    renderListItem: wd.renderListItem,
    renderEvents: wd.renderEvents,
    renderCheckBox: wd.renderCheckBox,
    renderLoop: wd.renderLoop,
    renderSelect: wd.renderSelect,
    updateOptions: function (option, newValue, item) {
      let value = typeof newValue === 'string' ? newValue.trim() : newValue
      option.value = value
      this.optionsUpdate()
    },
    /*
     render widgets
     */
    drawWidgets: function (h) {
      let attrs = []
      this.widgets.forEach(function (item) {
        const widget = item.widget
        const option = wd.getOptByProp(widget.prop, this.options[item.key])
        attrs.push(this.widgetRender(widget, option, h))
      }, this)
      return attrs
    },
    /*
    init attributes value
    */
    init: function (rootKey, nodeWidgets) {
      const orginalKey = this.options.root
      // same item do nothing
      if (orginalKey === rootKey) return
      if (rootKey == null) {
        this.clean()
        return
      }
      this.options = {
        root: rootKey
      }
      if (!Array.isArray(nodeWidgets)) return
      this.widgets = nodeWidgets
      nodeWidgets.forEach(function (item) {
        const nodeKey = item.key // vnode id
        let vOpt = item.opt // vnode saved option
        const widget = item.widget
        let value = (vOpt && vOpt.value) ? vOpt.value : widget.defaultVal
        if (typeof value === 'object') value = deepClone(value)
        let mode = vOpt ? vOpt.mode : null
        const opt = new wd.Option(widget.prop, value, widget.type, mode)
        if (this.options.hasOwnProperty(nodeKey)) {
          this.options[nodeKey].push(opt)
        } else {
          this.$set(this.options, nodeKey, [opt])
        }
      }, this)
      this.refreshView()
    },
    refreshView: function () {
      this.show = false
      this.$nextTick(function () {
        this.show = true
      })
    },
    listDelete: function (list, index) {
      list.splice(index, 1)
      this.optionsUpdate()
      this.refreshView()
    },
    listAdd: function (list, item) {
      list.push({
        el: item.el,
        options: []
      })
      this.optionsUpdate()
      this.refreshView()
    },
    // destory attrView
    remove: function (rootKey) {
      const orginalKey = this.options.root
      // clean all if there are same keys
      if (orginalKey === rootKey) this.clean()
      return orginalKey
    },
    // clean view
    clean: function () {
      this.options = {}
      this.widgets = []
      this.moduleName = null
      this.events = {
        show: false,
        data: []
      }
      this.temp = {}
      this.refreshView()
    },
    optionsUpdate: function () {
      EditorUtils.emitOptionsUpdate(this.options)
    }
  }
}



