<template>
  <div id="mainContainer" class="canvas-container">
    <div class="editor-title">
      <span class="page-id">{{id.name}}</span>
      <div class="editor-choose-width">
        <span>实际屏宽</span>
        <el-select v-model="reviewWidth" placeholder="屏宽(px)" style="width:80px">
          <el-option :value="414" label="iPone6 Plus"></el-option>
          <el-option :value="740"></el-option>
          <el-option :value="768" label="iPad"></el-option>
          <el-option :value="960"></el-option>
          <el-option :value="1080"></el-option>
          <el-option :value="1280"></el-option>
        </el-select>
      </div>
      <div class="editor-title-button">
        <el-button
          size="small"
          type="success"
          @click="switchMode"
          :disabled="disabled"
        >{{mode==='html'?'JS代码':'页面编辑'}}</el-button>
        <el-button size="small" type="success" @click="preview" :disabled="disabled">预览</el-button>
        <el-button size="small" type="success" @click="save" :disabled="disabled">保存</el-button>
      </div>
    </div>
    <div
      id="editorContainer"
      @click="blankClick"
      :class="{'normal':zoomPercent>=0.8}"
      v-show="mode==='html'"
    >
      <div id="itemContainer" class="container-layout-zone" :style="itemContainerStyle">
        <editor-dyn-container :vNode="vNode" v-if="show"></editor-dyn-container>
      </div>
    </div>
    <div id="jsContainer" v-show="!mode||mode==='code'"></div>
  </div>
</template>
<script>
import main, { EditorUtils, Editor } from "../assets/js/winning-editor";
import EditorDynView from "./EditorDynView";
import EditorDialogContainer from "./container/DialogContainer";
import * as dragUtils from "../assets/js/dragUtils";

import * as cst from "../assets/js/const";
import pagePool from "../assets/js/pagePool";
import vdata from "../assets/js/vdata";
import CodeEditor from "../assets/js/codeEditor";

var editor;
var interacts = [];
var ce;
export default {
  name: "ContainerView",
  props: {
    id: {
      required: true
    }
  },
  data() {
    return {
      vNode: null,
      reviewWidth: 1280, // review page width
      show: true,
      zoomPercent: 0,
      savePOShow: false,
      isNew: true,
      previewShow: false,
      type: null,
      mode: "",
      disabled: false
    };
  },
  components: {
    "editor-dyn-container": EditorDynView,
    "editor-dialog-container": EditorDialogContainer // editor container for dialog
  },
  mounted: function() {
    this.initData(); // create editor instance
  },
  destroyed: function() {
    editor = null;
    interacts.forEach(item => {
      item.unset();
    });
    interacts = [];
  },
  methods: {
    initData: function() {
      let self = this;
      this.id.name = main().name;
      this.id.key = main().id;
      self.createEditor();
      self.ready();
      // pagePool.getPageInfo(
      //   this.id.module,
      //   this.id.pageId,
      //   (isSucc, pageInfo) => {
      //     if (!isSucc) {
      //       self.$Editor.message('error', cst.NOT_LOAD_PAGE_ERROR)
      //       self.disabled = true
      //       return
      //     }
      //     let conf = pageInfo.conf
      //     if (conf.width) self.reviewWidth = conf.width
      //     self.type = conf.type
      //     let pageData = pageInfo.data
      //     self.createEditor(pageData)
      //     self.ready(pageInfo.script)
      //   }
      // )
    },
    ready: function(script) {
      this.type = this.type || this.pageType;
      EditorUtils.onItemClick(this.itemClick); // component item click listener
      EditorUtils.onItemDelete(this.itemDelete); // component item delete listener
      EditorUtils.onOptionsUpdate(this.updateOptions); // component attributes changed listener
      EditorUtils.onLayoutChange(this.updateLayout); // component attributes changed listener
      this.mode = "html";
      ce = CodeEditor("jsContainer", script);
      this.$nextTick(function() {
        this.calculateZoompercet(this.reviewWidth);
      });
    },
    createEditor: function(editorInput) {
      if (!editorInput && this.type === cst.DIALOG_MODE) editorInput = "dialog";
      editor = new Editor(editorInput);
      this.vNode = editor.getData();
      this.initDragAction();
    },
    initDragAction: function() {
      interacts.push(dragUtils.initEditorItemDrag(this.dragMoveListener));
      interacts.push(this.initDragLayoutZone());
      interacts.push(this.initDragItemZone());
      interacts.push(this.initDragItemMove());
    },
    // caluate zoomPercent for editor container
    calculateZoompercet: function(reviewWidth) {
      const containerWidth = document.getElementById("mainContainer")
        .offsetWidth;
      if (containerWidth >= reviewWidth) {
        this.zoomPercent = 1;
      } else {
        this.zoomPercent = containerWidth / reviewWidth;
      }
    },
    dragMoveListener: function(event) {
      var target = event.target;
      var x =
        (parseFloat(target.getAttribute("data-x")) || 0) +
        event.dx / this.zoomPercent;
      var y =
        (parseFloat(target.getAttribute("data-y")) || 0) +
        event.dy / this.zoomPercent;
      target.style.webkitTransform = target.style.transform =
        "translate(" + x + "px, " + y + "px)";
      target.setAttribute("data-x", x);
      target.setAttribute("data-y", y);
    },
    initDragLayoutZone: function() {
      let dragLayout = dragUtils.initDragZone(
        ".editor-layout-zone,.container-layout-zone",
        ".layout-draggable"
      );
      dragLayout.on("dragenter", this.dragZoneEnter);
      dragLayout.on("drop", event => {
        var dropZone = event.target;
        dropZone.classList.remove("drop-enter");
        const dragItemType = event.relatedTarget.getAttribute("type");
        const nodeKey = dropZone.getAttribute("nodekey");
        const action = dropZone.getAttribute("action");
        const slotName = dropZone.getAttribute("slot-name");
        editor.addLayout(dragItemType, nodeKey, action, slotName);
        this.dragZoneLeave(event);
      });
      dragLayout.on("dragleave", this.dragZoneLeave);
      return dragLayout;
    },
    initDragItemZone: function() {
      var dragZoneIneract = dragUtils.initDragZone(
        ".item-zone,.item-zone-empty",
        ".item-draggable"
      );
      dragZoneIneract.on("dragenter", this.dragZoneEnter);
      dragZoneIneract.on("drop", event => {
        const dropZone = event.target;
        dropZone.classList.remove("drop-enter");
        const targetNodeKey = dropZone.getAttribute("nodekey");
        const layout = dropZone.getAttribute("layout");
        const dragItemType = event.relatedTarget.getAttribute("type");
        editor.add(dragItemType, targetNodeKey, layout);
      });
      dragZoneIneract.on("dragleave", this.dragZoneLeave);
      return dragZoneIneract;
    },
    initDragItemMove: function() {
      var dragZoneIneract = dragUtils.initDragZone(
        ".editor-item",
        ".editor-item-move"
      );
      dragZoneIneract.on("dragenter", this.dragZoneEnter);
      dragZoneIneract.on("drop", event => {
        const dropZone = event.target;
        dropZone.classList.remove("drop-enter");
        const targetNodeKey = dropZone.getAttribute("nodekey");
        const layoutTo = dropZone.getAttribute("layout");
        const movedNodeKey = event.relatedTarget.getAttribute("nodekey");
        const layoutFrom = event.relatedTarget.getAttribute("layout");
        editor.move(layoutTo, layoutFrom, movedNodeKey, targetNodeKey);
      });
      dragZoneIneract.on("dragleave", this.dragZoneLeave);
      return dragZoneIneract;
    },
    dragZoneEnter: function(event) {
      var dropZone = event.target;
      dropZone.classList.add("drop-enter");
    },
    dragZoneLeave: function(event) {
      const dropZone = event.target;
      dropZone.classList.remove("drop-enter");
    },
    /*
    Trigger attribute view change if click component item
    */
    itemClick: function(nodeKey) {
      var widgets = editor.getWidgetByKey(nodeKey);
      let attr = this.$parent.$refs["attrView"];
      attr.init(nodeKey, widgets);
    },
    /*
   Trigger attribute view change if click component item
   */
    itemDelete: function(nodeKey, compKey) {
      editor.deleteNodeByKey(nodeKey);
      const attrView = this.$parent.$refs.attrView;
      const originClickedKey = attrView.remove(compKey);
      // revoke click event for ui-editor-item render problem
      if (originClickedKey != null) {
        this.$nextTick(function() {
          EditorUtils.emitItemClick(originClickedKey);
        });
      }
    },
    /*
    Trigger component attribute changing
    */
    updateOptions: function(options) {
      editor.updateVnodeOpts(options);
    },
    updateLayout: function(editInfo) {
      editor.editLayout(editInfo);
    },
    blankClick: function() {
      EditorUtils.emitItemClick();
    },
    preview: function() {
      let pageInfo = this.createPageInfo();
      this.$emit("preview", pageInfo);
    },
    save: function() {
      let pageInfo = this.createPageInfo();
      let self = this;
      main().save(this.id.module, pageInfo, function(isSucc, err) {
        if (!isSucc) {
          let message = err || cst.SYSTEM_ERROR;
          self.$Editor.message("warning", message);
        } else if (isSucc) {
          self.$Editor.message("success", cst.SAVE_SUCCESS);
          self.isNew = false;
          self.$emit("savePage", self.pageId);
          self.savePOShow = false;
        }
      });
    },
    createPageInfo: function() {
      let domData = this.vNode.toData();
      let scriptCode = ce.getCode();
      let conf = { type: this.type, width: this.reviewWidth };
      return vdata.createPageInfo(this.id.key, domData, scriptCode, conf);
    },
    switchMode: function() {
      this.mode = this.mode === "html" ? "code" : "html";
    }
  },
  watch: {
    reviewWidth: function(newWitdh) {
      this.calculateZoompercet(newWitdh);
    }
  },
  computed: {
    itemContainerStyle: function() {
      let container = document.getElementById("eidtor-content");
      const height = !container
        ? 0
        : (container.offsetHeight - 46) / this.zoomPercent - 5;

      let style = {
        transform: "scale(" + this.zoomPercent + ")",
        width: this.reviewWidth + "px",
        height: height + "px"
      };
      if (this.zoomPercent < 1) {
        style["transform-origin"] = "0 0";
      } else {
        style["height"] = "100%";
      }
      return style;
    }
  }
};
</script>
<style scoped>
.empty {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  text-align: center;
  width: 323px;
  height: 240px;
  font-size: 20px;
  color: #c5c5c5;
}

.img {
  background-image: url(../assets/images/form_empty.png);
  width: 323px;
  height: 215px;
}

.editor-title {
  height: 45px;
  line-height: 45px;
  font-size: 12px;
  padding: 0 0px 0 6px;
  border-bottom: solid 1px #e0e0e0;
  background-color: #fff;
  position: relative;
}

.editor-choose-width {
  display: inline-block;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  width: 200px;
}

.editor-title-button {
  display: inline-block;
  position: absolute;
  right: 5px;
}

#editorContainer {
  position: relative;
  flex: 1;
  background-color: gray;
}

#itemContainer {
  background-color: white;
  overflow-y: auto;
  margin: auto;
  position: absolute;
  left: 0;
  right: 0;
}

#jsContainer {
  flex: 1;
}

.new-page-id {
  color: red;
}

.page-id {
  font-size: 16px;
}
</style>



