import CodeMirror from 'codemirror'
import beautify from 'js-beautify'
import jshint from 'jshint'
import 'codemirror/lib/codemirror.css'
import 'codemirror/theme/base16-dark.css'
import 'codemirror/mode/javascript/javascript'
import 'codemirror/addon/lint/lint'
import 'codemirror/addon/lint/lint.css'
import 'codemirror/addon/lint/javascript-lint'
import 'codemirror/addon/edit/matchbrackets'
import 'codemirror/addon/edit/closebrackets'
import pagePool, {require} from '@/assets/js/pagePool'
import * as cst from '@/assets/js/const'
import * as utils from '@/assets/js/utils'

let codeModuleTpl = 'var exports;{0}\nreturn exports;'
let emptyCode = 'exports={\nprops:{},\nmounted:function(){},\nmethods:{}\n\n\n};'

if (window) {
  window.JSHINT = jshint.JSHINT
}

function CodeEditor(cm) {
  this.cm = cm
  let self = this
  this.cm.on('blur', function (event) {
    self.save()
  })
  this.cm.setOption('extraKeys', {
    'Shift-Ctrl-F': function (cm) {
      self.codeFormat()
    }
  })
}

CodeEditor.prototype.getCode = function () {
  return this.cm.getDoc().getValue()
}

CodeEditor.prototype.setCode = function (codeStr) {
  return this.cm.getDoc().setValue(codeStr)
}

CodeEditor.prototype.codeFormat = function () {
  let res = beautify(this.getCode())
  this.setCode(res)
}
/**
 * save and effect js code to temp place
 *
 */
CodeEditor.prototype.save = function () {
  let res
  try {
    res = codeCompile(this.getCode())
  } catch (e) {
    utils.message('error', e.toString())
    console.error(e)
  }
  pagePool.register(cst.TEMP_MODULE_NAME, res)
}

export default function create(id, code) {
  let inputCode = code || emptyCode
  let cm = CodeMirror(document.getElementById(id), {
    value: inputCode,
    mode: 'javascript',
    lineNumbers: true,
    theme: 'base16-dark',
    gutters: ['CodeMirror-lint-markers'],
    lint: true,
    autoCloseBrackets: true,
    matchBrackets: true
  })
  return new CodeEditor(cm)
}

export function codeCompile(code) {
  try {
    let moduleCode = utils.format(codeModuleTpl, code)
    /* eslint-disable no-new-func */
    let exportF = new Function('require', moduleCode)
    return exportF(require)
  } catch (e) {
    throw new Error('JS代码编译错误 [' + e + ']')
  }
}



