<template>
  <div>
    <div class="editor-preview-bg" v-if="pageType!==dialogMode">
      <div class="editor-preivew-header">
        <el-button type="success" size="small" @click="close">关闭预览</el-button>
      </div>
      <div :style="'width:'+width+'px'" class="editor-preivew-page">
        <div id="pagePre"></div>
      </div>
    </div>
    <div v-else id="pagePre"></div>
  </div>
</template>
<script>
import pc from '@/assets/js/pageCompile'
import { DIALOG_MODE } from '@/assets/js/const'
export default {
  name: 'preview',
  data() {
    return {
      width: 0,
      pageType: '',
      dialogMode: DIALOG_MODE
    }
  },
  mounted: function() {},
  methods: {
    open: function(pageInfo) {
      try {
        this.width = pageInfo.conf.width
        let PageClass = pc.buildPage(pageInfo)
        let vm = new PageClass()
        this.pageType = pageInfo.conf.type
        this.$nextTick(function() {
          this.mount(vm)
        })
      } catch (e) {
        console.error(e)
        this.$Editor.message('error', e.toString())
      }
    },
    handleClose: function(done) {
      done()
    },
    mount: function(vm) {
      if (this.pageType === this.dialogMode) vm.visible = true
      vm.$mount('#pagePre')
    },
    close: function() {
      this.$emit('close')
    }
  }
}
</script>
<style scoped>
.editor-preview-bg {
  position: absolute;
  background-color: #243a54;
  width: 100%;
  margin: 0;
  padding: 0;
  top: 0;
  bottom: 0;
}

.editor-preivew-header {
  text-align: center;
}

.editor-preivew-page {
  bottom: 0;
  position: absolute;
  left: 0;
  right: 0;
  top: 30px;
  background-color: #fff;
  margin: auto;
}
</style>




