import EditorHome from './views/EditorHome'
import main from './assets/js/winning-editor'
import './assets/css/editor.css'
import * as utils from './assets/js/utils'
import WnContainer from '@/components/WnContainer'

let editor = main()
const install = function (Vue, opts = {}) {
  if (install.installed) return
  Vue.component('wn-html-editor', EditorHome)
  Vue.component('wn-container', WnContainer)
  Vue.prototype.$Editor = utils
}
editor.install = install
editor.component = function () {
  return EditorHome
}
module.exports = editor



