 <template>
    <div>
        <h2 class="refuseForm-h2">
            条形码:{{ TXM }}
            <span>【{{ PATNAME }}】</span>
        </h2>
        <h2 class="refuseForm-reason">拒绝原因：
            <i @click="showClick">选择原因</i>
        </h2>
        <ul v-if="show" @click="showClick">
            <li v-for="(item,index) in selectData" :key="index">{{ item.NAME}}</li>
        </ul>
    </div>
</template>
<script>
const plugin = {
    label: '条形码',
    options: [

    ]
}

export default {
    name: 'TestComp',
    plugin: plugin,
    props: {
        TXM: '',
        PATNAME: '',
        selectData: {
            type: Array
        }
    },
    data() {
        return {
            show: false
        }
    },
    methods: {
        showClick: function() {
            console.log(this.selectData)
            this.show = !this.show
        }
    }
}
</script>
<style scoped>
h2 {
    font-size: 100%;
    font-weight: normal;
}

.refuseForm-reason i {
    float: right;
    color: #4e97fe;
    text-align: right;
    cursor: pointer;
}

.refuseForm-reason>ul li:hover {
    color: #fff;
    background-color: #4e97fe;
    cursor: pointer;
}

.refuseForm-reason>ul li {
    padding: 0px 10px;
    line-height: 2;
}

.refuseForm-reason>ul {
    position: absolute;
    right: 0px;
    max-height: 200px;
    overflow-y: auto;
    border: 1px solid #d2d2d2;
    background-color: #fff;
    z-index: 1;
}
</style>


