<template>
  <div id="frame-west">
    <div v-for="(group,index) in groups" :key="index">
      <div class="widget-group"> {{group.name}}</div>
      <div class="widget-item" v-for="item in group.items" :key="item.type">
        <a :class="item.type.indexOf('layout')>=0?'layout-draggable':'item-draggable'" :type="item.type">
          <i class="icon-widget-text"></i>
          <span>{{item.label}}</span>
        </a>
      </div>
    </div>
  </div>
</template>
<script>
import * as dragUtils from '../assets/js/dragUtils'
import components from '../assets/json/components.json'
import pagePool from '../assets/js/pagePool'
import * as utils from '../assets/js/utils'
import * as cst from '../assets/js/const'
import vdata from '../assets/js/vdata'

export default {
  name: 'CompView',
  data() {
    return {
      groups: []
    }
  },
  mounted: function() {
    this.initDragableItems()
    dragUtils.initCompDrag()
  },
  methods: {
    initDragableItems: function() {
      Object.keys(components).forEach(group => {
        const groupComps = components[group]
        const items = []
        Object.keys(groupComps).forEach(itemKey => {
          const compItem = groupComps[itemKey]
          if (!compItem.hidden) {
            items.push({ type: itemKey, label: compItem.label })
          }
        })
        let groupName = group === 'plugin' ? '扩展组件' : group
        this.groups.push({ name: groupName, items: items })
      })
    }
  }
}
</script>
<style scoped>
.frame-inner-list {
  padding: 8px 0;
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  overflow-y: auto;
}

.widget-group {
  padding: 8px 12px;
  font-size: 14px;
  clear: both;
}

.widget-item {
  font-size: 12px;
  display: block;
  width: 45%;
  line-height: 26px;
  position: relative;
  float: left;
  left: 0;
  white-space: nowrap;
  padding: 2px;
  color: #333;
}

.widget-item a:hover {
  border: 1px dashed #77bdff;
  color: #0db3a6;
}

.widget-item a {
  display: block;
  cursor: move;
  background: #f4f6fc;
  border: 1px solid #f4f6fc;
  text-align: center;
}

#frame-west ul {
  position: relative;
  overflow: hidden;
  padding: 0 10px 10px 10px;
}

.clone-item {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 80px;
  z-index: 9999;
}

</style>



