<template>
  <div class="editor-full">
    <editor-header v-on:switch="onTestSwitch" v-if="header"></editor-header>
    <div id="eidtor-content" class="editor-content" v-show="showEditor">
      <comp-view ref="compView" class="comp-container"></comp-view>
      <container-view v-if="conShow" :id="key" @preview="onPreview"></container-view>
      <comp-attr-view :pageId="key.pageId" v-if="attrShow" ref="attrView" class="attr-container"></comp-attr-view>
    </div>
    <preview ref="preview" @close="closePreview" v-if="showPreview"></preview>
    <div v-if="testShow">
      <div id="test-container"></div>
    </div>
  </div>
</template>
<script>
import EditorHeader from "../components/EditorHeader.vue";
import CompView from "./CompView";
import ContainerView from "./ContainerView";
import CompAttrView from "./CompAttrView";
import { EditorUtils } from "../assets/js/winning-editor";
import PreView from "./PreView";
import { DIALOG_MODE } from "@/assets/js/const";
import main from "../assets/js/winning-editor";

export default {
  name: "editorFrame",
  props: {
    header: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      conShow: true,
      attrShow: true,
      key: {},
      testShow: false,
      showEditor: true,
      showPreview: false
    };
  },
  components: {
    preview: PreView,
    "comp-view": CompView,
    "editor-header": EditorHeader,
    "container-view": ContainerView,
    "comp-attr-view": CompAttrView
  },
  destroyed: function() {
    EditorUtils.clearEventBus();
  },
  methods: {
    initEditor: function(key) {
      if (!key.pageId || this.key.pageId === key.pageId) return;
      this.key = Object.assign({}, key);
      this.refreshEditorPage();
    },
    refreshEditorPage: function() {
      this.conShow = false;
      this.attrShow = false;
      EditorUtils.clearEventBus();
      this.$nextTick(function() {
        this.conShow = true;
        this.attrShow = true;
      });
    },
    onTestSwitch: function() {
      this.showEditor = !this.showEditor;
      this.testShow = !this.testShow;
      if (!this.testShow) return;
      this.$nextTick(function() {
        main().createForm({
          el: "#test-container",
          data: JSON.parse(sessionStorage.getItem("data"))
        });
      });
    },
    onPreview: function(pageInfo) {
      if (pageInfo.conf.type !== DIALOG_MODE) {
        this.showEditor = false;
      }
      this.showPreview = false;
      this.$nextTick(function() {
        this.showPreview = true;
        this.$nextTick(function() {
          this.$refs["preview"].open(pageInfo);
        });
      });
    },
    closePreview: function() {
      this.showEditor = true;
      this.showPreview = false;
    }
  }
};
</script>
<style>
</style>



