import Vue from 'vue'
import * as cst from './const'

let vm = new Vue()

export function objectPath(obj, path, valueObj) {
  var splitKey = path.split('.')
  var target = obj
  var attr
  var v = valueObj == null ? undefined : valueObj.v
  var dv = valueObj == null ? null : valueObj.dv
  for (var i = 0, len = splitKey.length; i < len; i++) {
    attr = splitKey[i]
    var last = i === len - 1
    // create attribute
    if (target.hasOwnProperty(attr) === false) {
      if (last) {
        createAttr.call(this, target, attr, dv)
      } else {
        createAttr.call(this, target, attr, {})
      }
    }
    // if have value ,set value
    if (last && v !== undefined) {
      createAttr(target, attr, v)
    }
    // embed object may be null ,set empty object first
    if (target[attr] == null && !last) {
      createAttr.call(this, target, attr, {})
    }
    target = target[attr]
  }
  return {
    v: target
  }
}

function createAttr(target, attr, value) {
  if (this == null) {
    target[attr] = value
  } else {
    this.$set(target, attr, value)
  }
}

export function deepClone(source) {
  if (source == null) {
    return
  }
  return JSON.parse(JSON.stringify(source))
}

/**
 * handle v-model attirbutes
 * @param {*} dataType handle defualt value to bind data
 * @param {*} attr vnode attr
 * @param {*} obData  observer data
 * @param {*} path bind path
 * @param {*} bindProp element bind attibute ,e.g. input is binding 'value'
 */
export function handleDataBind(dataType, attr, obData, path, bindProp) {
  handleOneWayBind.call(this, dataType, attr, obData, path, bindProp)
  // if bindProp is 'value' ,considering two way bind
  if (bindProp === 'value') {
    attr.set('on', {
      input: function (value) {
        objectPath.call(this, obData, path, {
          v: value
        })
        this.$emit('input', value)
      }.bind(this)
    })
  }
}

/**
 * handle one single way bind
 */
function handleOneWayBind(dataType, attr, obData, path, bindProp) {
  const defaultValue = handleDefaultValue(dataType)
  let newObj = {}
  newObj[bindProp] = objectPath.call(this, obData, path, {
    dv: defaultValue
  }).v
  attr.set('props', newObj)
}

/**
 * create new attribute for observer data
 * @param {*} dataType default value for attribute
 * @param {*} obData  observer data
 * @param {*} path create path
 */
export function createObjPath(dataType, obData, path) {
  if (!path) return
  const defaultValue = handleDefaultValue(dataType)
  objectPath(obData, path, {
    dv: defaultValue
  })
}

function handleDefaultValue(dataType) {
  if (dataType === 'array') return []
  if (dataType === 'object') return {}
  return null
}

export function createDefaultRow() {
  let defaultRow = []
  for (let i = 0; i < 4; i++) {
    defaultRow.push({
      span: 6
    })
  }
  return defaultRow
}

export function message(type, message) {
  vm.$message({
    message: message,
    type: type
  })
}

export function deleteConfirm(confirmAction, confirmMessage) {
  confirmMessage = confirmMessage || '是否确定删除?'
  vm.$confirm(confirmMessage, '提示', {
    confirmButtonText: '确定',
    cancelButtonText: '取消',
    type: 'warning'
  }).then(() => {
    confirmAction()
    message('success', '删除成功')
  }).catch(() => {})
}

export function strEndWith(str, s) {
  if (!str || !s || s.length > str.length) return false
  if (str.substring(str.length - s.length) === s) {
    return true
  } else {
    return false
  }
}

export function getProps(prop) {
  if (propType(prop)) return prop.substr(1, prop.length)
  return prop
}

export function propType(prop) {
  if (typeof prop !== 'string') return
  if (prop.charAt(0) === ':') return cst.V_BIND
  if (prop.charAt(0) === '@') return cst.V_ON
}

export function eventType(event) {
  let pos = propType(event)
  if (pos === cst.V_BIND) return 'props'
  if (pos === cst.V_ON) return 'on'
  return 'nativeOn'
}

let placeHoderExp = /\{\d+?\}/g
export function format(message) {
  if (!message) return null
  let args = arguments
  return message.replace(placeHoderExp, function (pos) {
    let i = Number(pos.substring(1, 2)) + 1
    return args[i]
  })
}




