import Vue from 'vue'
import pc from './pageCompile'
const editorPageListUrl = '/dyn-editor/list/'
const editorPageUrl = '/dyn-editor/page/'
const editorCompileUrl = '/dyn-editor/compile/'
const updateModuleUrl = '/dyn-editor/u-m/'
const updatePageUrl = '/dyn-editor/u-p/'
let action = {} // api

// edit page list
let pageList
let regPagePool = {} // store editor page registered succ or not
let modulePool = {} // outside js moudule(methods) for  edit page

let pluginPool = {} // store plugins

// loadPageClass()

function PageModule(name, pages) {
  this.name = name
  this.pages = pages.map(page => {
    return {
      parent: name,
      name: page,
      st: false
    }
  })
}

function loadPageList(callback) {
  const xhr = new XMLHttpRequest()
  xhr.onreadystatechange = () => {
    if (xhr.readyState === 4 && xhr.status === 200) {
      let pages = JSON.parse(xhr.responseText)
      pageList = []
      pages.forEach(page => {
        pageList.push(new PageModule(page.m, page.list))
      })
      callback(pageList)
    }
  }
  xhr.open('GET', editorPageListUrl + '@')
  xhr.send()
}

action.updatePageStatus = function (moduleId) {
  if (!moduleId) return
  const xhr = new XMLHttpRequest()
  xhr.onreadystatechange = () => {
    if (xhr.readyState === 4 && xhr.status === 200 && xhr.responseText) {
      let ids = pc.getCompiledIds(xhr.responseText)
      pageList.forEach(pageModule => {
        if (pageModule.name !== moduleId) return
        pageModule.pages.forEach(page => {
          if (ids.indexOf(page.name) >= 0) page.st = true
        })
      })
    }
  }
  xhr.open('GET', editorCompileUrl + moduleId)
  xhr.send()
}

action.getModulePage = function (moduleId, callback) {
  if (!moduleId) return
  const xhr = new XMLHttpRequest()
  xhr.onreadystatechange = () => {
    if (xhr.readyState === 4 && xhr.status === 200 && xhr.responseText) {
      let pageData = pc.compile(xhr.responseText)
      callback(pageData)
    }
  }
  xhr.open('GET', editorCompileUrl + moduleId)
  xhr.send()
}

function savePageInfo(moduleId, info, callback) {
  const xhr = new XMLHttpRequest()
  xhr.onreadystatechange = xhrStateChange(xhr, callback)
  xhr.open('POST', editorPageUrl + moduleId + '/' + info.id)
  xhr.setRequestHeader('Content-Type', 'application/json')
  xhr.send(JSON.stringify(info))
}

function savePageCompile(moduleId, info, callback) {
  const xhr = new XMLHttpRequest()
  let pageJs = pc.buildPageJs(info)
  xhr.onreadystatechange = xhrStateChange(xhr, (isucc, responseText) => {
    callback(isucc, responseText)
  })
  xhr.open('POST', editorCompileUrl + moduleId)
  xhr.setRequestHeader('Content-Type', 'application/json')
  let content = {
    id: info.id,
    tpl: pc.entryTpl,
    data: pageJs
  }
  xhr.send(JSON.stringify(content))
}

/**
 * save edit page data
 */
action.updatePage = function (moduleId, info, callback) {
  savePageInfo(moduleId, info, function (isSucc, err) {
    if (isSucc) {
      savePageCompile(moduleId, info, callback)
    } else {
      callback(isSucc, err)
    }
  })
}
/**
 * get edit page id list
 */
action.getList = function (callback) {
  if (callback) {
    loadPageList(callback)
  } else {
    return pageList
  }
}

action.savePage = function (data, moduleId, pageId, pageInfo, callback) {
  const xhr = new XMLHttpRequest()
  xhr.onreadystatechange = xhrStateChange(xhr, callback)
  let validator
  if (pageId) {
    validator = validatePage(data, moduleId, pageId)
    let url = data ? updatePageUrl + data.parent + '/' + data.name + '/' : editorPageUrl + moduleId + '/'
    url = url + pageId
    xhr.open('POST', url)
  } else {
    validator = validateModule(data, moduleId)
    let url = data ? updateModuleUrl + data.name + '/' : editorPageListUrl
    url = url + moduleId
    xhr.open('GET', url)
  }
  if (!validator || validator.res) {
    if (pageInfo) {
      xhr.setRequestHeader('Content-Type', 'application/json')
      xhr.send(JSON.stringify(pageInfo))
    } else {
      xhr.send()
    }
  } else {
    callback(false, validator.msg)
  }
}

function validateModule(data, moduleId) {
  let validator = {
    res: true
  }
  for (let index in pageList) {
    let pageModule = pageList[index]
    if (data === pageModule) continue
    if (pageModule.name === moduleId) {
      validator.res = false
      validator.msg = `模块[${moduleId}]已存在`
    }
  }
  return validator
}

function validatePage(data, moduleId, pageId) {
  let validator = {
    res: true
  }
  for (let index in pageList) {
    let pageModule = pageList[index]
    pageModule.pages.forEach(page => {
      if (data === page) return
      if (page.name === pageId) {
        validator.res = false
        validator.msg = `页面[${pageId}]已存在`
      }
    })
    if (!validator.res) break
  }
  return validator
}

action.deletePage = function (moduleId, pageId, callback) {
  const xhr = new XMLHttpRequest()
  xhr.onreadystatechange = xhrStateChange(xhr, callback)
  if (pageId) {
    xhr.open('DELETE', editorPageUrl + moduleId + '/' + pageId)
  } else {
    xhr.open('DELETE', editorPageListUrl + moduleId)
  }
  xhr.send()
}

/**
 * get edit page by page id
 */
action.getPageInfo = function (moduleId, pageId, cb) {
  const xhr = new XMLHttpRequest()
  xhr.onreadystatechange = xhrStateChange(xhr, (isSucc, responseText) => {
    isSucc ? cb(isSucc, JSON.parse(responseText)) : cb(isSucc)
  })
  xhr.open('GET', editorPageUrl + moduleId + '/' + pageId)
  xhr.send()
}

/**
 * store module(logic part)
 */
action.register = function (moduleName, target) {
  modulePool[moduleName] = target
}

/**
 * get registered logic module list
 */
action.getModuleList = function () {
  let moduleList = []
  Object.keys(modulePool).forEach(moduleName => {
    moduleList.push(moduleName)
  })
  return moduleList
}

/**
 * get all methods in logic module
 */
action.getModuleMethods = function (moduleName) {
  let methods = []
  let instance = modulePool[moduleName]
  if (instance == null || instance.methods == null) {
    return methods
  }
  return Object.keys(instance.methods)
}

/**
 * get logic module by name
 */
action.getModule = function (moduleName) {
  return modulePool[moduleName]
}

/**
 * save plugin info
 */
action.putPlugin = function (name, plugin) {
  pluginPool[name] = plugin
}

/**
 * get plugin info
 */
action.getPlugin = function (name) {
  return pluginPool[name]
}

/**
 * get edit page by page id
 */
action.regPage = function (pageId, compileAction) {
  if (pageId) regPagePool[pageId] = false
  try {
    let result = compileAction()
    result.forEach(page => {
      if (!page.name) {
        console.error(' page name can\'t be null')
        return
      }
      Vue.component(page.name, page.data)
      regPagePool[page.name] = true
    })
  } catch (e) {

  }
}

function xhrStateChange(xhr, callback) {
  return () => {
    if (xhr.readyState !== 4) return
    let result = true
    if (xhr.status !== 200) result = false
    if (callback) callback(result, xhr.responseText)
  }
}

/**
 * load outside js module
 * @param {*} name module name
 */
global._import = function (name) {
  return action.getModule(name)
}

export default action



